import { LightFlowBackComponent } from './../light/light-components/light-flow/light-flow-back/light-flow-back.component';
import { WorkDetailPage } from './../pages/work/work-detail/work-detail';
//import { Deeplinks } from '@ionic-native/deeplinks/ngx';
import { LightComponentsModule, LightFlowHandleComponent, LightFlowDeliverComponent, LightMemberDetailComponent } from './../light/light-components/light-components.module';
import { LogPage } from './../pages/log/log';
import { NgModule, ErrorHandler } from "@angular/core";
import { BrowserModule } from "@angular/platform-browser";
import { IonicApp, IonicModule, IonicErrorHandler } from "ionic-angular";
import { MyApp } from "./app.component";

//动画库
import { ModalFromBottomLeave, ModalFromBottomEnter } from "./modal-transition";
import { Config } from "ionic-angular";

//基础页面
import { LoginPage, TabsPage } from "../pages/common/index";
import { HomePage } from "../pages/home/home";
import { AccountPage } from "../pages/account/account";
import { WorkPageComponent } from "../pages/work/work-page.component"

//基础插件
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

///ajax请求管理
import { CommonService } from "./CommonService";

//lightout库
import {
  LightOutModule,
  LightListComponent,
  LightContactComponent
} from "../light/light-out/light-out.module";
import { SearchPage } from "../pages/search/search";
import { WorkNewProcessListPage } from '../pages/work/work-new-process-list/work-new-process-list';
import { WorkNewProcessPage } from '../pages/work/work-new-process/work-new-process';
import { WorkFlowChartPage } from '../pages/work/work-flow-chart/work-flow-chart';
import { EmailListPage } from '../pages/home/email-list/email-list';
import { EmailDetailPage } from '../pages/home/email-detail/email-detail';
import { NewListPage } from '../pages/home/new-list/new-list';
import { NewDetailPage } from '../pages/home/new-detail/new-detail';
import { NoticeListPage } from '../pages/home/notice-list/notice-list';
import { NoticeDetailPage } from '../pages/home/notice-detail/notice-detail';
import { MessageListPage } from '../pages/home/message-list/message-list';
import { EquipmentListPage } from '../pages/home/equipment-list/equipment-list';
import { EquipmentEditPage } from '../pages/home/equipment-edit/equipment-edit';
import { EquipmentDetailPage } from '../pages/home/equipment-detail/equipment-detail';
import { EmailModifyComponent } from '../light/light-pages/light-person/email-modify/email-modify.component';
import { PasswordModifyComponent } from '../light/light-pages/light-person/password-modify/password-modify.component';
import { AdvisoryPage } from '../pages/account/advisory/advisory';
import { LightPersonComponent } from '../light/light-pages/light-person/light-person.component';
import { EnterManuallyPage } from '../pages/home/enter-manually/enter-manually';
import { LightFeedbackComponent } from '../light/light-pages/light-feedback/light-feedback.component';
import { LightMessageComponent } from '../light/light-pages/light-message/light-message.component';
import { LightMessageContentComponent } from '../light/light-pages/light-message/light-message-content/light-message-content.component';
import { LogDetailPage } from '../pages/log/log-detail/log-detail';
import { SearchDetailPage } from '../pages/search/search-detail/search-detail';


var entryComponents = [
    MyApp,
    LoginPage,
    // tab相关页面
    TabsPage,
    HomePage,
    WorkPageComponent,
    AccountPage,
    LightContactComponent,
    SearchPage,
    LogPage,
    // 【深层页面】
    // 工作圈相关
    WorkDetailPage,
    WorkNewProcessListPage,
    WorkNewProcessPage,
    WorkFlowChartPage,
    LightFlowBackComponent,
    LightFlowHandleComponent,
    LightFlowDeliverComponent,
    // 通知消息相关
    EmailListPage,
    EmailDetailPage,
    NewListPage,
    NewDetailPage,
    NoticeListPage,
    NoticeDetailPage,
    MessageListPage,
    // 设备相关
    EquipmentListPage,
    EquipmentEditPage,
    EquipmentDetailPage,
    EmailModifyComponent,
    // 个人相关
    PasswordModifyComponent,
    AdvisoryPage,
    LightPersonComponent,
    // 通讯录相关
    LightMemberDetailComponent,
    // 项目搜素相关
    SearchDetailPage,
    // 工作日志相关
    LogDetailPage,
    // 额外
    LightFeedbackComponent,
    LightMessageComponent,
    LightMessageContentComponent,
    EnterManuallyPage
]

@NgModule({
  declarations: entryComponents,
  imports: [
    BrowserModule,
    LightOutModule,
    LightComponentsModule,
    IonicModule.forRoot(MyApp, {
      backButtonIcon: "ios-arrow-back-outline",//按钮图标样式
      backButtonText: "返回",//按钮内容
      tabsHideOnSubPages: "true",
    })
  ],
  bootstrap: [IonicApp],
  entryComponents: entryComponents,
  exports: [LightListComponent],
  providers: [
    StatusBar,
    SplashScreen,
    CommonService,
    { provide: ErrorHandler, useClass: IonicErrorHandler }
  ]
})
export class AppModule {

  constructor(public config: Config) {
    this.setCustomTransitions();
  }

  private setCustomTransitions() {
    this.config.setTransition("modal-from-bottom-enter", ModalFromBottomEnter);
    this.config.setTransition("modal-from-bottom-leave", ModalFromBottomLeave);
  }

}



