import { Component } from '@angular/core';
import { IonicPage, NavController } from 'ionic-angular';
import { LightProviders } from '../../../../light/light-out/light-out.module';

@IonicPage()
@Component({
    selector: 'password-modify',
    templateUrl: 'password-modify.component.html'
})
export class PasswordModifyComponent {

    oldPassword: string;

    newPassword: string;

    confirmPassword: string;

    constructor(
        public navCtrl: NavController,
        public lightProviders: LightProviders,
    ) {

    }

    submit() {
        if (this.newPassword == this.confirmPassword) {
            this.lightProviders.lightUser.updatePassword(this.newPassword, this.oldPassword).then((value) => {
                console.log(value);
                if (value) {
                    this.lightProviders.lightPop.errMessage('修改密码成功');
                }
            }).catch(err => {
                this.lightProviders.lightPop.errMessage(err);
            })
        } else {
            this.lightProviders.lightPop.errMessage('两次密码不一致');
        }

    }

}



