import { LightNav } from './../../../light-providers/providers/lightNav';
import { Component, Input, OnInit } from '@angular/core';
import { LightWriterService, LightWriterFlow } from '../../light-writer/index';
import { LightReadService } from '../../light-read/index';
import { LightPop, LightLoading } from '../../../light-providers/light-providers.module';
import { LightJsonUtil, LightBrowserUtil } from '../../../light-util/light-util.module';
import { NavController } from 'ionic-angular';
import { LightFlowBackComponent, LightFlowHandleComponent, LightFlowDeliverComponent } from '..';

@Component({
    selector: 'light-flow',
    templateUrl: 'light-flow.component.html'
})
export class LightFlowComponent implements OnInit {

    ///流程的对象ID
    @Input() flowObjectID: string = "";

    ///展现菜单的标识
    @Input() showMenuFlag: boolean = false;

    @Input() curStepTitle: string;

    //控制开头
    showFlag: boolean = false;

    ///是否第一个环节展现
    fristStepShow: boolean = false;

    ///表单读取的数据信息
    readFormData: any = null;

    ///写入流程的操作
    writerFlow: LightWriterFlow = null;

    /**
     * 流程控制器初始化
     * @param {LightWriterService} lightWriterService
     * @param {LightReadService} lightReadService
     * @param {NavController} navCtrl
     * @memberof LightFlowComponent
     */
    constructor(
        public lightWriterService: LightWriterService,
        public lightReadService: LightReadService,
        public navCtrl: NavController,
        public lightpop: LightPop,
        private lightLoading: LightLoading,
        public lightNav: LightNav
    ) {

    }

    /**
      * 组件初始化
      * @memberof LightContentComponent
      */
    ngOnInit(): void {
        // 计算滚动盒子高度
        let headerHeight = LightBrowserUtil.isWeixinBrowser() ? 0 : 61;
        let height = document.body.clientHeight - headerHeight;
        document.querySelector('.lightFlowContent').setAttribute('style', 'margin-top: '+ headerHeight +'px;' + 'height: '+ height +'px')
        document.querySelector('.lightFlowScroll').setAttribute('style', 'height: '+ height +'px')

        // 加载表单数据启用遮罩层
        let loadctrl:any = this.lightLoading.showLoading('正在加载...');

        if (!!this.flowObjectID) {
            this.lightReadService.getReadForm(this.flowObjectID).then(value => {
                this.loadButton(loadctrl, LightJsonUtil.toObject(value));
            }).catch(err => {
                this.navCtrl.pop();
                loadctrl.instance && loadctrl.dismiss();
                this.lightpop.errMessage("该环节手机端不支持，请转电脑办理");
            });
        } else {
            loadctrl.instance && loadctrl.dismiss();
            console.log('参数丢失');
        };
    }

    /**
     *加载底部按钮
     *
     * @private
     * @param {*} loadctrl
     * @param {*} readFormData 内容模块数据
     * @memberof LightFlowComponent
     */
    private loadButton(loadctrl:any, readFormData:any) {
      if (!!this.showMenuFlag) {
        // 内容模块和按钮模块均加载
        this.lightWriterService.getWriterFlow(this.flowObjectID).then(value => {
            this.readFormData = readFormData;
            this.writerFlow = value;
            if (this.writerFlow.isFristStep()) {
                this.fristStepShow = true;
              } else {
                this.showFlag = true;
            };
            loadctrl.instance && loadctrl.dismiss();
        }).catch(errText => {
            this.readFormData = readFormData;
            loadctrl.instance && loadctrl.dismiss();
        });
      } else {
        // 只加载内容模块
        this.readFormData = readFormData;
        loadctrl.instance && loadctrl.dismiss();
      }
    }

    ///流程办理
    public workHandle(): void {
        this.navCtrl.push(LightFlowHandleComponent, { flow: this.writerFlow });
    }

    ///流程打回
    public workCallBack(): void {
        this.navCtrl.push(LightFlowBackComponent, { flow: this.writerFlow });
    }

    ///流程转交
    public workDeliver(): void {
        this.navCtrl.push(LightFlowDeliverComponent, { flow: this.writerFlow });
    }

}



