import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'light-round-progress',
  templateUrl: 'light-round-progress.component.html'
})
export class LightRoundProgressComponent implements OnInit {

  @Input() current: number;

  @Input() max: number;

  @Input() color = '#45ccce';

  @Input() background = '#eaeaea';

  /**
    * 加载 圆环进度条 插件命令: npm i angular-svg-round-progressbar --save
    * @memberof LightRoundProgressComponent
    */
  constructor(

  ) {

  }

  ngOnInit(): void {
    console.log(`light-round-progress init `)
  }

}



