import { Component, NgZone } from '@angular/core';
import { NavController, LoadingController, App } from 'ionic-angular';
import { LoginPage } from './../common/login/login';
import { LightApp, LightLoading, LightProviders, LightAppVersion, LightFileTransfer, LightFileOpen } from './../../light/light-out/light-out.module';
import { AdvisoryPage } from './advisory/advisory';
import { LightPersonComponent } from '../../light/light-pages/light-person/light-person.component';

@Component({
  selector: 'page-account',
  templateUrl: 'account.html',
})
export class AccountPage {

  //用户名称
  userName: string;

  public appVersion: any;

  // 下载信息属性
  loaded: number;
  total: number;
  downloading = false;

  constructor(
    public navCtrl: NavController,
    public providers: LightProviders,
    public loadingCtrl: LoadingController,
    public lightLoading: LightLoading,
    public lightFileTransfer: LightFileTransfer,
    public lightFileOpen: LightFileOpen,
    public lightAppVersion: LightAppVersion,
    public app: App,
    public lightApp: LightApp ,
    public lightProviders: LightProviders,
    public _zone: NgZone
  ) {
  }

  public ngOnInit(): void {
    this.userName = this.providers.lightUser.getUserName();
    this.lightAppVersion.getVersionNumber().then(data => {
      this.appVersion = data;
    });
  }

  //更新的操作
  update(): void {
    if (this.downloading) return;
    // 检测版本，弹框确认是否进入下载
    this.lightApp.updateAppFn(true, true, (data)=> {
      this._zone.run(() => {
        this.loaded = data.loaded;
        this.total = data.total;
        this.downloading = this.loaded == this.total ? false : true;
      })
    })
  }

  //二维码分享
  showShare() {
    var baseUrl = this.lightProviders.lightHttp.getUrl("MobileHtml/WriterDimensionalImage");
    var contentData = "<div><img src='" + baseUrl + "'></div>";
    let alert = this.lightProviders.lightPop.alertCtrl.create({
      //title: '二维码分享',
      message: contentData,
      buttons: [
        {
          text: '关闭',
          role: 'cancel',
          handler: () => {
            //console.log('不进行更新');
          }
        }
      ]
    });
    alert.present();
  }

  //意见反馈
  showAdvisory() {
    this.navCtrl.push(AdvisoryPage);
  }
  showPersonInfo() {
    this.navCtrl.push(LightPersonComponent);
  }

  //退出登录
  logout() {
    this.lightApp.logoutPage(LoginPage, this.app);
  }

}



