import { Injectable } from "@angular/core";
import { Platform } from "ionic-angular";
import { LightNation } from "../common/lightNation";
import { FileOpener } from "@ionic-native/file-opener";

/**
 * 本地npm的安装:npm install --save @ionic-native/file-opener
 * 加载ionic插件:ionic cordova plugin add cordova-plugin-file-opener2
 * 平台:Android | iOS | Windows | Windows Phone 8
 * 本地打开对象信息
 * @memberof LightOpen
 * @extends {LightNation}
 */
@Injectable()
export class LightFileOpen extends LightNation {
  constructor(private fileOpener: FileOpener, private platform: Platform) {
    super();
  }

  /**
   * 打开文件对象
   *
   * @param {string} path
   * @memberof LightOpen
   */
  openFile(path: string, defaultResult: any = null): Promise<any> {
    var that = this;
    if (!!!defaultResult) {
    }
    var result: Promise<any> = this._getDefaultResult(defaultResult);
    if (!!path) {
      if (this.platform.is("cordova")) {
        result = new Promise(function(resolve, reject) {
          var ext = that._getFileExt(path);
          var type = that._getFileType(ext);
          that.fileOpener
            .open(path, type)
            .then(result => {
              resolve(result);
            })
            .catch(errText => {
              reject(errText);
            });
        });
      }
    }
    return result;
  }

  /**
   * 得到文件的扩展名称
   * @private
   * @param {string} str
   * @returns {string}
   * @memberof LightOpen
   */
  private _getFileExt(str: string): string {
    var d = /\.[^\.]+$/.exec(str);
    return d.toString();
  }

  /**
   * 得到文件类型
   * @private
   * @param {string} path
   * @returns {string}
   * @memberof LightOpen
   */
  private _getFileType(ext: string): string {
    var result = "";
    if (!!ext) {
      switch (ext) {
        case ".txt":
          result = "text/plain";
          break;
        case ".jpg":
        case ".png":
        case ".gif":
        case ".bmp":
        case ".jpeg":
          result = "image/*";
          break;
        case ".pdf":
          result = "application/pdf";
          break;
        case ".doc":
        case ".docx":
          result = "application/msword";
          break;
        case ".xls":
        case ".xlsx":
          result = "application/vnd.ms-excel";
          break;
        case ".ppt":
        case ".pptx":
          result = "application/vnd.ms-powerpoint";
          break;
        case ".apk":
          result = "application/vnd.android.package-archive";
          break;
        case ".dwg":
          result = "application/x-autocad";
          break;
        default:
          result = "application/octet-stream";
      }
    }
    return result;
  }
}



