import { Directive, OnInit, OnDestroy, Renderer2, ElementRef, Renderer, Input } from '@angular/core';
import { Content } from 'ionic-angular';

@Directive({
  selector: '[hide-processbar]' // Attribute selector
})
export class HideProcessbarDirective implements OnInit, OnDestroy {

  /**
   *滚动组件的实例
   *
   * @memberof HideProcessbarDirective
   */
  @Input('hide-processbar') content: Content;

  // 隐藏页脚
  listenHideFuc: () => void;
  // 显示页脚
  listenShowFuc: () => void;
  // 滚动组件的底部边距
  marginBottom: any;

  constructor(
    private elementRef: ElementRef,
    private renderer: Renderer,
    private renderer2: Renderer2
  ) {
    console.log('HideProcessbarDirective Start...');
  }

  ngOnInit() {
    // 监听软键盘弹起
    this.listenHideFuc = this.renderer2.listen(
      'window',
      'keyboardWillShow',
      this.hideProcessbar.bind(this)
    );
    // 监听软键盘隐藏
    this.listenShowFuc = this.renderer2.listen(
      'window',
      'keyboardWillHide',
      this.showProcessbar.bind(this)
    )
  }

  ngOnDestroy() {
    // 取消监听
    this.listenHideFuc()
    this.listenShowFuc()
  }

  /**
   *软键盘弹起，隐藏页脚
   *
   * @memberof HideProcessbarDirective
   */
  hideProcessbar() {
    // 隐藏页脚
    let processbarEle = this.elementRef.nativeElement;
    this.renderer.setElementStyle(processbarEle, 'display', 'none');
    // 保存滚动组件的底边距并清空
    let scrollContentEle = this.content._elementRef.nativeElement.querySelector('.scroll-content');
    this.marginBottom = scrollContentEle.style['margin-bottom'];
    this.renderer.setElementStyle(scrollContentEle, 'margin-bottom', '0');
  }

  /**
   *软键盘消失，显示页脚
   *
   * @memberof HideProcessbarDirective
   */
  showProcessbar() {
    // 显示页脚
    let processbarEle = this.elementRef.nativeElement;
    this.renderer.setElementStyle(processbarEle, 'display', '');
    // 设置回原来的滚动组件的底边距
    let scrollContentEle = this.content._elementRef.nativeElement.querySelector('.scroll-content');
    this.renderer.setElementStyle(scrollContentEle, 'margin-bottom', this.marginBottom);
  }

}



