import { Injectable } from "@angular/core";
import { Platform } from "ionic-angular";
import {
  DeviceMotion,
  DeviceMotionAccelerometerOptions,
  DeviceMotionAccelerationData
} from "@ionic-native/device-motion";
import { LightNation } from "../common/lightNation";
import { Subscription } from "rxjs/Subscription";

/**
 * 本地npm的安装:npm install --save @ionic-native/device-motion
 * 加载ionic插件:ionic cordova plugin add cordova-plugin-device-motion
 * 平台:Android | BlackBerry 10 | Browser | Firefox OS | iOS | Tizen | Ubuntu | Windows | Windows Phone 8
 * 设备动作
 * @export
 * @class LightPermissisons
 * @extends {LightNation}
 */
@Injectable()
export class LightMotion extends LightNation {
  ///motion参数信息
  private options: DeviceMotionAccelerometerOptions = null;

  private _MotionSubscription: Subscription; //控制摇一摇的变量

  /**
   * @memberof LightMotion
   */
  constructor(private platform: Platform, private deviceMotion: DeviceMotion) {
    super();
    this.options = { frequency: 1000 };
  }

  //开启摇一摇监听
  public watchAcceleration(
    handler: (data: DeviceMotionAccelerationData) => void
  ): any {
    this.clearAcceleration();
    if (this.platform.is("cordova")) {
      return (this._MotionSubscription = this.deviceMotion
        .watchAcceleration(this.options)
        .subscribe((acceleration: DeviceMotionAccelerationData) =>
          handler(acceleration)
        ));
    } else {
      return;
    }
  }

  //关闭监听
  public clearAcceleration(): void {
    !!this._MotionSubscription && this._MotionSubscription.unsubscribe();
  }
}



