import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { UserObject } from './common/UserObject';
import { LightNav } from '../../light-providers/providers';
import { LightMemberDetailComponent } from '.';

@Component({
  selector: 'light-contact',
  templateUrl: 'light-contact.component.html'
})
//contact的控件
export class LightContactComponent {

  /**
   * 联系人的组件
   * @memberof LightContactComponent
   */
  constructor(
    public navCtrl: NavController,
    public lightNav: LightNav
  ) {

  }

  /**
   *
   * 联系人选择
   * @memberof LightContactComponent
   */
  contactSelect(userObject: UserObject): void {
    this.navCtrl.push(LightMemberDetailComponent, {
      userName: userObject.Name,
      organzieName: userObject.organizeName,
      title: userObject.Title
    })
  }

  /**
   *获取微信返回到当前页面时，需要还原的页面参数
   *
   * @private
   * @returns
   * @memberof LightContactComponent
   */
  private getTabData() {
    let tabData = {
      tabIndex: 2,
    };
    return tabData;
  }

}



