import { LightHttp, LightNav } from './../../../light/light-out/light-out.module';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

@IonicPage()
@Component({
  selector: 'page-work-flow-chart',
  templateUrl: 'work-flow-chart.html',
})
export class WorkFlowChartPage {

  code: string;

  chartData: any;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public lightHttp: LightHttp,
    public lightNav: LightNav
  ) {
    this.code = this.navParams.data.objectID;
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad WorkFlowChartPage');
  }

  ngOnInit() {
    this.lightHttp.get("Flow/GetFlowStepLog", { code: this.code }).then((data) => {
      this.chartData = JSON.parse(data);
    })
  }

}



