import { Injectable } from "@angular/core";
import { Platform } from "ionic-angular";
import { NativeAudio } from "@ionic-native/native-audio";
import { LightNation } from "../common/lightNation";

/**
 * 本地npm的安装:npm install --save @ionic-native/native-audio
 * 加载ionic插件:ionic cordova plugin add cordova-plugin-nativeaudio
 * 平台:Android | Browser | iOS
 * 原生音频
 * @memberof LightAudio
 */
@Injectable()
export class LightAudio extends LightNation {
  private musicList: any[] = null;

  public get MusicList(): string[] {
    return this.musicList;
  }

  constructor(private nativeAudio: NativeAudio, private platform: Platform) {
    super();
    this.musicList = [];
  }

  //预加载音乐
  public preloadSimple(id: string, path: string): Promise<any> {
    return new Promise<any>((resolve, reject) => {
      if (this.platform.is("cordova")) {
        this.nativeAudio
          .preloadSimple(id, path)
          .then(() => {
            this.musicList.push(id);
            resolve(id);
          })
          .catch(err => {
            reject(err);
          });
      } else {
        reject("非手机环境");
      }
    });
  }

  //高定制预加载音乐
  public preloadComplex(
    id: string,
    path: string,
    volume: number,
    voices: number,
    delay: number
  ): Promise<any> {
    return new Promise<any>((resolve, reject) => {
      if (this.platform.is("cordova")) {
        this.nativeAudio
          .preloadComplex(id, path, volume, voices, delay)
          .then(() => {
            this.musicList.push(id);
            resolve(id);
          })
          .catch(err => {
            reject(err);
          });
      } else {
        reject("非手机环境");
      }
    });
  }

  //无限循环音乐
  public loopAudios(id: string): void {
    let index = this.musicList.indexOf(id);
    if (this.platform.is("cordova") && index != -1) {
      this.nativeAudio.loop(id);
    }
  }

  //从内存中卸载音乐
  public unloadAudios(id: string): void {
    let index = this.musicList.indexOf(id);
    if (this.platform.is("cordova") && index != -1) {
      this.nativeAudio.unload(id);
      this.musicList.splice(this.musicList[index], 1);
    }
  }

  //播放音乐
  public playAudio(id: string, handler?: Function): void {
    let index = this.musicList.indexOf(id);
    if (this.platform.is("cordova") && index != -1) {
      this.nativeAudio.play(id, handler);
    }
  }

  //暂停音乐
  public stopAudio(id: string): void {
    let index = this.musicList.indexOf(id);
    if (this.platform.is("cordova") && index != -1) {
      this.nativeAudio.stop(id);
    }
  }

  //设置音量
  public setVolumeForComplexAsset(id: string, volume: number): void {
    let index = this.musicList.indexOf(id);
    if (this.platform.is("cordova") && index != -1) {
      this.nativeAudio.setVolumeForComplexAsset(id, volume);
    }
  }
}



