import { Injectable } from "@angular/core";
import { Platform } from "ionic-angular";
import { HotCodePush } from "@ionic-native/hot-code-push";
import { LightNation } from "../common/lightNation";

/**
 * 本地npm的安装:npm install --save @ionic-native/hot-code-push
 * 本地npm的插件CLI:npm install -g cordova-hot-code-push-cli
 * 加载ionic插件:ionic cordova plugin add cordova-hot-code-push-plugin
 * 平台:Android | iOS
 * 热更新
 * @param {HotCodePush} hotCodePush
 * @memberof LightHotCodePush
 */
@Injectable()
export class LightHotCodePush extends LightNation {
  constructor(private platform: Platform, public hotCodePush: HotCodePush) {
    super();
  }

  /**
   * 热更新推荐显示规则
   *
   * 。。。
   *
   * 三点全绿     表示不需要更新
   * 颜色轮转     表示正在更新
   * 三点全红     表示等待重进更新
   * 三点全黄     表示更新失败
   *
   */

  // @Input() defaultColor: string;

  // @Input('appHighlight') highlightColor: string;

  // //初始化热更新监听事件
  // public initCodePush(): void {

  //     this.hotStates(progress);

  //     this.hotCodePush.isUpdateAvailableForInstallation().then((value) => {

  //         console.log('表示等待重进更新' + value);
  //         this.hotStates('red');

  //     }).catch((err) => {

  //         console.log(err);

  //         if (err['code'] == 1) {
  //             console.log('表示不需要更新');
  //             this.hotStates('green');
  //         } else {
  //             console.log('表示更新失败');
  //             this.hotStates('yellow');
  //         }

  //     });
  // }

  // private hotStates(color: string) {
  //     this.el.nativeElement.style.backgroundColor = color;
  // }

  // chcp_updateIsReadyToInstall - 成功加载新版本并准备安装时发送。
  // chcp_updateLoadFailed - 当插件无法从服务器加载更新时发送。错误详细信息附加到事件中。
  // chcp_nothingToUpdate - 当我们从服务器成功加载应用程序配置时发送，但没有新的可用。
  // chcp_beforeInstall - 即将安装更新时发送。
  // chcp_updateInstalled - 成功安装更新后发送。
  // chcp_updateInstallFailed - 更新安装失败时发送。错误详细信息附加到事件中。
  // chcp_nothingToInstall - 无任何安装时发送。可能之前没有任何东西被加载。
  // chcp_beforeAssetsInstalledOnExternalStorage - 当插件即将开始在外部存储上安装捆绑内容时发送。
  // chcp_assetsInstalledOnExternalStorage - 插件成功从外部存储上的bundle中复制Web项目文件时发送。您很可能只会将其用于调试目的。甚至从来没有。
  // chcp_assetsInstallationError - 当插件无法从外部存储上的bundle复制文件时发送。如果发生这种情况 - 插件将无法正常工作。当设备上没有足够的可用空间时可能会发生。错误详细信息附加到事件中。
  // document.addEventListener('chcp_updateIsReadyToInstall', this.onUpdateReady, false);

  /**
   * requestApplicationUpdate(message)
   * 显示包含通过商店（App Store或Google Play）更新应用程序的请求的对话框。
   * 信息 string 要在对话框中显示的消息
   * 返回： Promise<any>当用户重定向到商店时解析，如果用户拒绝则拒绝。
   */
  requestApplicationUpdate(message: any) {
    var results = this._getDefaultResult("");
    if (this.platform.is("cordova")) {
      results = this.hotCodePush.requestApplicationUpdate(message);
    }
    return results;
  }

  /**
   * fetchUpdate(options)
   * 从服务器端下载更新。
   * 选项 HotCodePushRequestOptions 请求的其他选项。如果未设置 - 使用config.xml的首选项。
   * 返回： Promise < any > 如果有可用更新，则解析，否则拒绝。
   */
  fetchUpdate(options: any) {
    var results = this._getDefaultResult("");
    if (this.platform.is("cordova")) {
      results = this.hotCodePush.fetchUpdate(options);
    }
    return results;
  }

  /**
   * installUpdate()
   * 如果有任何要安装的东西，请安装更新。
   * 返回： Promise<any>安装更新时解析。
   */
  installUpdate() {
    var results = this._getDefaultResult("");
    if (this.platform.is("cordova")) {
      results = this.hotCodePush.installUpdate();
    }
    return results;
  }

  /**
   * isUpdateAvailableForInstallation()
   * 检查是否已加载更新并准备安装。
   * 返回： Promise<HotCodePushUpdate>如果更新准备就绪，则解析，如果没有更新，则拒绝。
   */
  isUpdateAvailableForInstallation() {
    var results = this._getDefaultResult("");
    if (this.platform.is("cordova")) {
      results = this.hotCodePush.isUpdateAvailableForInstallation();
    }
    return results;
  }

  /**
   * getVersionInfo()
   * 获取有关应用程序版本的信息。
   * 返回： Promise<HotCodePushVersion>解析有关版本的信息
   */
  getVersionInfo() {
    var results = this._getDefaultResult("");
    if (this.platform.is("cordova")) {
      results = this.hotCodePush.getVersionInfo();
    }
    return results;
  }

  /**
   * onUpdateIsReadyToInstall()
   * 成功加载新版本并准备安装时发送的事件。
   * 返回： Observable<HotCodePushEventData>
   */
  onUpdateIsReadyToInstall() {
    var results: any = "";
    if (this.platform.is("cordova")) {
      results = this.hotCodePush.onUpdateIsReadyToInstall();
    }
    return results;
  }

  /**
   * onUpdateLoadFailed()
   * 插件无法从服务器加载更新时发送的事件。错误详细信息附加到事件中。
   * 返回： Observable<HotCodePushEventData>
   */
  onUpdateLoadFailed() {
    var results: any = "";
    if (this.platform.is("cordova")) {
      results = this.hotCodePush.onUpdateLoadFailed();
    }
    return results;
  }

  /**
   * onNothingToUpdate()
   * 当我们从服务器成功加载应用程序配置时发送的事件，但没有新的可用。
   * 返回： Observable<HotCodePushEventData>
   */
  onNothingToUpdate() {
    var results: any = "";
    if (this.platform.is("cordova")) {
      results = this.hotCodePush.onNothingToUpdate();
    }
    return results;
  }

  /**
   * onBeforeInstall()
   * 即将安装更新时发送的事件。
   * 返回： Observable<HotCodePushEventData>
   */
  onBeforeInstall() {
    var results: any = "";
    if (this.platform.is("cordova")) {
      results = this.hotCodePush.onBeforeInstall();
    }
    return results;
  }

  /**
   * onUpdateInstalled()
   * 成功安装更新后发送的事件。
   * 返回： Observable<HotCodePushEventData>
   */
  onUpdateInstalled() {
    var results: any = "";
    if (this.platform.is("cordova")) {
      results = this.hotCodePush.onUpdateInstalled();
    }
    return results;
  }

  /**
   * onUpdateInstallFailed()
   * 更新安装失败时发送的事件。错误详细信息附加到事件中。
   * 返回： Observable<HotCodePushEventData>
   */
  onUpdateInstallFailed() {
    var results: any = "";
    if (this.platform.is("cordova")) {
      results = this.hotCodePush.onUpdateInstallFailed();
    }
    return results;
  }

  /**
   * onNothingToInstall()
   * 无任何安装时发送的事件。可能之前没有任何东西被加载。
   * 返回： Observable<HotCodePushEventData>
   */
  onNothingToInstall() {
    var results: any = "";
    if (this.platform.is("cordova")) {
      results = this.hotCodePush.onNothingToInstall();
    }
    return results;
  }

  /**
   * onBeforeAssetsInstalledOnExternalStorage()
   * 当插件即将开始在外部存储上安装捆绑包内容时发送的事件。
   * 返回： Observable<HotCodePushEventData>
   */
  onBeforeAssetsInstalledOnExternalStorage() {
    var results: any = "";
    if (this.platform.is("cordova")) {
      results = this.hotCodePush.onBeforeAssetsInstalledOnExternalStorage();
    }
    return results;
  }

  /**
   * onAssetsInstalledOnExternalStorage()
   * 插件成功从外部存储上的bundle中复制Web项目文件时发送的事件。您很可能只会将其用于调试目的。甚至从来没有。
   * 返回： Observable<HotCodePushEventData>
   */
  onAssetsInstalledOnExternalStorage() {
    var results: any = "";
    if (this.platform.is("cordova")) {
      results = this.hotCodePush.onAssetsInstalledOnExternalStorage();
    }
    return results;
  }

  /**
   * onAssetsInstallationError()
   * 当插件无法从外部存储上的bundle复制文件时发送的事件。如果发生这种情况 - 插件将无法正常工作。当设备上没有足够的可用空间时可能会发生。
   * 返回： Observable<HotCodePushEventData>
   */
  onAssetsInstallationError() {
    var results: any = "";
    if (this.platform.is("cordova")) {
      results = this.hotCodePush.onAssetsInstallationError();
    }
    return results;
  }
}



