import { Injectable } from "@angular/core";
import { Platform } from "ionic-angular";
import { LightNation } from "../common/lightNation";
import { ImagePicker, ImagePickerOptions } from "@ionic-native/image-picker";

/**
 * 本地npm的安装:npm install --save @ionic-native/image-picker
 * 加载ionic插件:ionic cordova plugin add cordova-plugin-image-picker
 * 加载ionic插件:ionic cordova plugin add cordova-plugin-telerik-imagepicker --variable PHOTO_LIBRARY_USAGE_DESCRIPTION="your usage message"
 * 平台:Android | iOS
 * 图像选择器
 * @param {ImagePicker} imagePicker
 * @memberof LightImagePicker
 */
@Injectable()
export class LightImagePicker extends LightNation {
  /**
   * 参数对象
   *
   * @private
   * @type {ImagePickerOptions}
   * @memberof LightImagePicker
   */
  private _options: ImagePickerOptions;

  constructor(private platform: Platform, private imagePicker: ImagePicker) {
    super();
    this._options = {
      maximumImagesCount: 6
      //width: IMAGE_SIZE,
      //height: IMAGE_SIZE,
      //quality: QUALITY_SIZE
    };
  }

  /**
   *从相册选取图片
   *
   * @param {*} [resultDefault=null]
   * @param {ImagePickerOptions} [option] 自定义相册参数
   * @returns {Promise<any>}
   * @memberof LightImagePicker
   */
  getPicture(resultDefault: any = null, option? :ImagePickerOptions): Promise<any> {
    if (!!!resultDefault) {
      resultDefault = "https://www.baidu.com/img/bd_logo1.png";
    }
    var result: Promise<any> = this._getDefaultResult(resultDefault);
    if (this.platform.is("cordova")) {
      result = new Promise((resolve, reject) => {
        if (option) Object.assign(this._options, option);
        this.imagePicker
          .getPictures(this._options)
          .then(results => {
            resolve(results); //返回选中多张图片数组
          })
          .catch(errText => {
            reject(errText);
          });
      });
    }
    return result;
  }
}



