import { Component } from '@angular/core';
import { IonicPage, NavController, ModalController, ViewController } from 'ionic-angular';
import { LightExit, LightScan, LightPop, LightStringUtil, LightJsonUtil } from '../../../light/light-out/light-out.module';
import { EquipmentDetailPage } from '../equipment-detail/equipment-detail';

/**
 * Generated class for the EquipmentListPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-equipment-list',
  templateUrl: 'equipment-list.html',
})
export class EquipmentListPage {

  ///标识字符串
  private _flagString: string = "AssetDocUNID=";

  constructor(public navCtrl: NavController, public viewController:ViewController, public modalCtrl: ModalController, public lightExit: LightExit, public lightScan: LightScan, public lightPop: LightPop) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad EquipmentListPage');
  }

  /**
   * 二维码的搜索操作
   * @private
   * @memberof HomePage
   */
  private qrCodeSearch(): void {
    this.lightScan.scan("{\"QRCodeRule\":\"AssetDocUNID=05BEE97B2A354677B0B5C97D926D2359\"}").then(result => {
      var flag: boolean = false;
      if (!!result) {
        var resultObject = LightJsonUtil.toObject(result);
        if (!!resultObject && !!resultObject['QRCodeRule']) {
          var content = this._qrCodeUrlCheck(resultObject['QRCodeRule']);
          if (!!content) {
            //this.viewController.dismiss();
            flag = true;
            this.navCtrl.push(EquipmentDetailPage, { code: content,id:"" });
          }
        }
      }
      if(!flag){
        this.lightPop.message("未查到该编码设备!");
      }
    }).catch(errText => {
    });
  }

  /**
   * 二维码的检查信息
   * @private
   * @memberof HomePage
   */
  private _qrCodeUrlCheck(urlDetail: string): string {
    var result: string = "";
    if (LightStringUtil.contain(urlDetail, this._flagString)) {
      var index = urlDetail.indexOf(this._flagString);
      result = urlDetail.substr(index + this._flagString.length, urlDetail.length - index - this._flagString.length);
    }
    return result;
  }


  goEnterId() {
    let modal = this.modalCtrl.create('EnterManuallyPage');
    modal.present();
  }
  /**
   * 转换为详情的信息
   * @private
   * @memberof HomePage
   */
  private goToDetail(): void {
    this.navCtrl.push(EquipmentDetailPage);
  }

}



