import { Component, Input, OnChanges } from "@angular/core";
import { NavController, AlertController } from "ionic-angular";
import {
  LightHttp,
  LightPop
} from "../../light-providers/light-providers.module";
import { LightStringUtil } from "../../light-util/light-util.module";
import {
  LightFile,
  LightFileTransfer,
  LightFileOpen
} from "../../light-nation/light-nation.module";

@Component({
  selector: "light-attach",
  templateUrl: "light-attach.component.html"
})
export class LightAttachComponent implements OnChanges {
  /**
   *附件信息数组
   *
   * @type {Array<any>}
   * @memberof LightAttachComponent
   */
  @Input() attachList: Array<{
    Code: string;
    FilesLength: string;
    filesLength: string;
    Name: string;
    fileName: string;
    Path: string;
    fileUrl: string;
    Status: string;
    current: number;
    downloading: boolean;
    timer: any;
  }>;

  // 进度条基础样式
  max: number = 1;

  /**
   * 附件的操作
   * @memberof LightAttachComponent
   */
  constructor(
    public navCtrl: NavController,
    public alertCtrl: AlertController,
    public lightHttp: LightHttp,
    public lightPop: LightPop,
    public lightFileOpen: LightFileOpen,
    public lightFileTransfer: LightFileTransfer,
    public lightFile: LightFile
  ) {}

  /**
   *
   * 等待父组件传入数据，进行渲染
   */
  ngOnChanges(): void {
    if (!!this.attachList) {
      // 初始化附件的下载状态
      this.attachList.forEach((attach, index) => {
        this.attachList[index].Status = "下载";
        this.lightFile
          .checkFile(this.attachList[index].Name)
          .then(data => {
            this.attachList[index].Status = data == true ? "打开" : "下载";
          })
          .catch(err => {
            this.attachList[index].Status = "下载";
          });
      });
    }
  }

  /**
   * 下载的操作
   *
   * @param {any} item
   * @param {any} index
   * @memberof LightAttachComponent
   */
  downloadFile(item: any, index: any) {
    if (this.attachList[index].Status == "打开") {
      // /var/mobile/Applications/<application UUID>/Documents/path/to/file  (iOS)
      // /storage/emulated/0/path/to/file                                    (Android)
      // 打开文件
      this.lightFile.getRootFilePath().then(data => {
        var path = data + item.Name;
        this.lightFileOpen
          .openFile(path)
          .then(data => {
            console.log("打开成功");
          })
          .catch(err => {
            this.lightPop.errMessage(err.message);
          });
      });
    } else {
      // 下载文件
      this.attachList[index].downloading = true;
      var url = item.Path || item.fileUrl;
      // 初始化进度条数值
      let current = 0;
      this.attachList[index].current = current;
      // 定时器，在下载期间更新下载进度
      let timer = setInterval(() => {
        this.attachList[index].current = current;
        if (this.attachList[index].current == 1) {
          clearInterval(timer);
        }
      }, 100);
      // 进行下载
      this.lightFileTransfer
        .download(url, item.Name || item.fileName, undefined, e => {
          current = e.loaded / e.total;
        })
        .then(data => {
          const alertSuccess = this.alertCtrl.create({
            title: `下载成功`,
            subTitle: `${item.Name} 下载到以下路径：${data}`,
            buttons: [
              {
                text: "确定",
                role: "ok",
                handler: () => {
                  console.log("ok clicked");
                }
              },
              {
                text: "打开",
                handler: () => {
                  this.lightFile.getRootFilePath().then(data => {
                    var path = data + item.Name;
                    this.lightFileOpen
                      .openFile(path)
                      .then(data => {
                        console.log("open", data);
                      })
                      .catch(err => {
                        this.lightPop.errMessage(err.message);
                        console.log(err);
                      });
                  });
                }
              }
            ]
          });
          alertSuccess.present();
          this.attachList[index].Status = "打开";
          this.attachList[index].downloading = false;
        })
        .catch(errText => {
          this.attachList[index].Status = "重新下载";
          this.attachList[index].downloading = false;
          console.log("下载失败" + errText);
        });
    }
  }

  /**
   * 判断文件类型，显示对应图片
   *
   * @param {any} input
   * @returns {*}
   * @memberof NewDetailPage
   */
  public fileTypeImage(input: any): any {
    var out = "";
    if (
      LightStringUtil.contain(input, "jpg") ||
      LightStringUtil.contain(input, "png") ||
      LightStringUtil.contain(input, "gif")
    ) {
      out = "jpg";
    } else if (
      LightStringUtil.contain(input, "ppt") ||
      LightStringUtil.contain(input, "pptx")
    ) {
      out = "ppt";
    } else if (LightStringUtil.contain(input, "txt")) {
      out = "txt";
    } else if (
      LightStringUtil.contain(input, "doc") ||
      LightStringUtil.contain(input, "docx") ||
      LightStringUtil.contain(input, "wps")
    ) {
      out = "word";
    } else if (
      LightStringUtil.contain(input, "xls") ||
      LightStringUtil.contain(input, "xlsx")
    ) {
      out = "xls";
    } else if (
      LightStringUtil.contain(input, "zip") ||
      LightStringUtil.contain(input, "7z")
    ) {
      out = "zip";
    } else if (LightStringUtil.contain(input, "pdf")) {
      out = "pdf";
    } else {
      out = "unknown";
    }
    out = "assets/img/attach/" + out + "_128.png";
    return out;
  }

  /**
   *将文件字节数转换成对应的大小
   *
   * @param {*} fileByte
   * @returns
   * @memberof LightAttachComponent
   */
  public getFileSize(fileByte: any) {
    var fileSizeByte = fileByte;
    var fileSizeMsg = "";
    if (fileSizeByte < 1048576)
      fileSizeMsg = (fileSizeByte / 1024).toFixed(2) + "KB";
    else if (fileSizeByte == 1048576) fileSizeMsg = "1MB";
    else if (fileSizeByte > 1048576 && fileSizeByte < 1073741824)
      fileSizeMsg = (fileSizeByte / (1024 * 1024)).toFixed(2) + "MB";
    else if (fileSizeByte > 1048576 && fileSizeByte == 1073741824)
      fileSizeMsg = "1GB";
    else if (fileSizeByte > 1073741824 && fileSizeByte < 1099511627776)
      fileSizeMsg = (fileSizeByte / (1024 * 1024 * 1024)).toFixed(2) + "GB";
    else fileSizeMsg = "文件超过1TB";
    return fileSizeMsg;
  }
}



