import { Component, ViewChild, OnInit, ElementRef, Renderer, Renderer2 } from '@angular/core';
import { NavController, Events, Keyboard } from 'ionic-angular';
import { Tabs } from 'ionic-angular/components/tabs/tabs';
import { LightContactComponent } from '../../../light/light-out/light-out.module';

import { HomePage } from '../../home/home';
import { WorkPageComponent } from '../../work/work-page.component';
import { AccountPage } from '../../account/account';

import { TabsServices } from './tabs.service';
import { LogPage } from '../../log/log';
import { SearchPage } from '../../search/search';

@Component({
  templateUrl: 'tabs.html',
  providers: []
})
export class TabsPage implements OnInit {
  @ViewChild('tabComponent') TabsCom: Tabs;

  tabRoots: {
    root: any,
    tabTitle: string,
    tabUrlPath: string,
    tabIcon: string
  }[];

  mb: any;

  // 隐藏tab栏
  listenHideFuc: () => void;
  // 显示tab栏
  listenShowFuc: () => void;

  constructor(
    public navCtrl: NavController,
    private elementRef: ElementRef,
    private renderer: Renderer,
    private renderer2: Renderer2,
    private event: Events,
    public keyboard: Keyboard
  ) {
    this.tabRoots = [{
      root: WorkPageComponent,
      tabTitle: '待办事务',
      tabUrlPath: 'work',
      tabIcon: 'ios-color-filter'
    }, {
      root: HomePage,
      tabTitle: '通讯交流',
      tabUrlPath: 'home',
      tabIcon: 'ios-chatbubbles-outline'
    }, {
    //   root: LightContactComponent,
    //   tabTitle: '通讯录',
    //   tabUrlPath: 'light-contact',
    //   tabIcon: 'ios-git-network-outline'
    // }, {
      root: SearchPage,
      tabTitle: '项目搜索',
      tabUrlPath: 'search',
      tabIcon: 'ios-search-outline'
    }, {
      root: LogPage,
      tabTitle: '工作日志',
      tabUrlPath: 'log',
      tabIcon: 'ios-git-network-outline'
    }];
  }


  ngOnInit() {
    TabsServices.setTabsInstance(this.TabsCom);

    // 监听软键盘弹起
    this.listenHideFuc = this.renderer2.listen(
      'window',
      'wxKeyboardShow',
      this.hideTab.bind(this)
    );
    // 监听软键盘隐藏
    this.listenShowFuc = this.renderer2.listen(
      'window',
      'wxKeyboardHide',
      this.showTab.bind(this)
    )
  }

  ngOnDestroy() {
    // 取消监听
    this.listenHideFuc()
    this.listenShowFuc()
  }

  hideTab() {
    let tabs = this.elementRef.nativeElement.querySelector('.tabbar');
    this.renderer.setElementStyle(tabs, 'display', 'none');
    let SelectTab = this.TabsCom.getSelected()._elementRef.nativeElement;
    let content = SelectTab.querySelector('.scroll-content');
    this.mb = content.style['margin-bottom'];
    this.renderer.setElementStyle(content, 'margin-bottom', '0')
    let footer = SelectTab.querySelector('ion-footer');
    this.renderer.setElementStyle(footer, 'display', 'none');
  }

  showTab() {
    let tabs = this.elementRef.nativeElement.querySelector('.tabbar');
    this.renderer.setElementStyle(tabs, 'display', '');
    let SelectTab = this.TabsCom.getSelected()._elementRef.nativeElement;
    let content = SelectTab.querySelector('.scroll-content');
    this.renderer.setElementStyle(content, 'margin-bottom', this.mb);
    let footer = SelectTab.querySelector('ion-footer');
    this.renderer.setElementStyle(footer, 'display', '');
  }
}



