import { LightFlowService, LightJsonUtil, LightStringUtil } from './../../../light/light-out/light-out.module';
import { Component } from '@angular/core';
import { IonicPage, NavController } from 'ionic-angular';
import { WorkNewProcessPage } from '../work-new-process/work-new-process';

@IonicPage()
@Component({
  selector: 'page-work-new-process-list',
  templateUrl: 'work-new-process-list.html',
})
export class WorkNewProcessListPage {

  dataList: any;

  sCode: any;

  constructor(
    public navCtrl: NavController,
    public flowServcie: LightFlowService
  ) {

  }

  /**
   * 内部初始化
   * @memberof WorkNewProcessListPage
   */
  ngOnInit() {
    this.flowServcie.getStartFlowList().then(value => {
      this.dataList = LightJsonUtil.toObject(value);
    });
  }

  newProcess(item) {
    this.navCtrl.push(WorkNewProcessPage, { item });
  }

  /**
   * 判断流程类型，显示对应图片
   *
   * @param {any} input
   * @returns {*}
   * @memberof NewDetailPage
   */
  public flowImage(input): any {
    var out = "";
    if (LightStringUtil.contain(input, "公文办理")) {
      out = "icon1";
    } else if (LightStringUtil.contain(input, "公章")) {
      out = "icon3";
    } else if (LightStringUtil.contain(input, "车辆使用申请单")) {
      out = "icon20";
    } else if (LightStringUtil.contain(input, "请假")) {
      out = "icon5";
    } else if (LightStringUtil.contain(input, "软硬件采购")) {
      out = "icon15";
    } else if (LightStringUtil.contain(input, "发文登记")) {
      out = "icon23";
    } else if (LightStringUtil.contain(input, "来文登记")) {
      out = "icon24";
    } else {
      out = "icon1";
    }
    out = "assets/img/icon-work/" + out + ".png";
    return out;
  }

}



