import { Component, Input, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';

@Component({
    selector: 'light-date',
    templateUrl: 'light-date.component.html'
})
export class LightDateComponent implements OnInit {

  @Input() dateArr: {
    'txt': string, // 字段名
    'value': string, // 默认值
    'type': string, // 字段类型
    'extend': {
      'fieldtype': string // 具体类型
    }
  }[];

  constructor() {}

  ngOnInit() {}

  /**
   *转换成ionic识别的日期格式
   *
   * @param {*} type
   * @memberof LightDateComponent
   */
  parseType(type) {
    switch (type) {
      case 'dateyymmdd':
        return 'YYYY/MM/DD';
      case 'dateyymm':
        return 'YYYY-MM-DD HH:mm';
      case 'datetime':
        return 'YYYY/MM';
      default:
        return false;
    }
  }

  /**
   *日期字段发生变化时，根据需求选择规则运算
   *
   * @param {string} fieldtype //日期类型
   * @param {NgForm} dateForm  //表单引用
   * @memberof LightDateComponent
   */
  dateChange(fieldtype: string, dateForm: NgForm) {
    switch (fieldtype) {
      case 'start':
        this.rule1(dateForm);
        break;
      case 'end':
        this.rule1(dateForm);
        break;
      default:
        break;
    }
  }

  /**
   *持续天数 = 结束时间 - 开始时间
   *
   * @param {NgForm} dateForm // 表单索引
   * @memberof LightDateComponent
   */
  rule1(dateForm: NgForm) {
    // 该规则运算涉及的字段具体类型
    let data = dateForm.value as {
      start: string, // 开始时间
      end: string, // 结束时间
      interval: string // 持续天数
    };
    let {start, end} = data;
    if(start && end && 'interval' in data) {
      this.dateArr.map((item => {
        if(item.extend.fieldtype == 'interval') {
          item.value = ((new Date(end).getTime() - new Date(start).getTime()) / 86400000).toFixed(1) + '天';
        }
      }))
    }
  }
}



