import { LogDetailPage } from './log-detail/log-detail';
import { Component } from "@angular/core";
import { IonicPage, NavController } from "ionic-angular";
import { LightConfig } from "../../light/light-providers/providers";

/**
 * Generated class for the LogPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: "page-log",
  templateUrl: "log.html"
})
export class LogPage {
  allLogDataUrl = "Log/GetAll.json";

  myLogDataUrl = "Log/GetMy.json";

  dataUrl: string = "";

  dataParam: any = {};

  datalist: Array<any> = [];

  segmentVal = "allLog";

  name = "大明";

  // 日期列表
  dayList = [];
  // 日期列表对应的日志索引
  dayIndex = [];

  constructor(
    public navCtrl: NavController,
    public lightConfig: LightConfig
  ) {}

  public ngOnInit(): void {}

  ionViewDidEnter() {
    this.lightConfig.baseUrl = "http://" + location.host + "/assets/json/"; //自定义测试json
    this.switchSeg();
  }

  ionViewWillLeave() {
    this.lightConfig.baseUrl = "http://172.16.50.135:27116/"; //城堪院内网
  }

  /**
   *变更日子列表的请求地址，触发组件更新
   *
   * @memberof LogPage
   */
  public switchSeg() {
    this.dayList = [];
    this.dayIndex = [];
    if (this.segmentVal == "allLog") {
      this.dataUrl = this.allLogDataUrl;
    } else if (this.segmentVal == "myLog") {
      this.dataUrl = this.myLogDataUrl;
    }
  }

  /**
   *是否在某日志上面显示日期
   *
   * @param {string} time 日志详细时间
   * @param {*} i 日志索引
   * @returns
   * @memberof LogPage
   */
  public showDay(time: string, i) {
    let inList = this.dayList.indexOf(time.split(" ")[0]) > -1;
    let inIndex = this.dayIndex.indexOf(i) > -1;
    if (!inList) {
      // 显示没有加载过的日期
      this.dayList.push(time.split(" ")[0]);
      this.dayIndex.push(i);
      return true;
    } else {
      if (inIndex) {
        // 日期加载过，但因为light-list组件的生命周期二次判定，此处基于添加历史对应二次判定逻辑
        return true;
      } else {
        // 加载过，且历史数组里没有这项目本身的索引记录，不显示
        return false;
      }
    }
  }

  /**
   *跳转日志详情页
   *
   * @memberof LogPage
   */
  public toLogDetail(CODE = false) {
      this.navCtrl.push(LogDetailPage, {CODE});
  }
}



