import { EquipmentListPage } from './equipment-list/equipment-list';
import { Component, SimpleChanges } from '@angular/core';
import { NavController } from 'ionic-angular';
import { LightApp, LightProviders, LightNav } from '../../light/light-out/light-out.module';
import { TabsServices } from '../common/tabs/tabs.service';
import { WorkNewProcessListPage } from '../work/work-new-process-list/work-new-process-list';
import { NewListPage } from './new-list/new-list';
import { NoticeListPage } from './notice-list/notice-list';
import { EmailListPage } from './email-list/email-list';
import { MessageListPage } from './message-list/message-list';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html',

})
export class HomePage {
  showIconText: number;
  imageUrl: string;
  rowLabel: string;

  public demandPoints: any;
  public layerdata: any;

  Todoflag: boolean = false;
  Doingflag: boolean = false;
  showTodoIconText: number;
  showDingIconText: number;

  constructor(
    public navCtrl: NavController,
    public providers: LightProviders,
    public lightNav: LightNav
  ) {
    //super(navCtrl, providers);
  }

  ngOnInit() {
    //获取待办个数
    this.providers.lightHttp.get("Flow/GetToDoCount").then(data => {
      console.log(data);
      if (data > 0) {
        this.Todoflag = true;
        this.showTodoIconText = data;
      } else {
        this.Todoflag = false;
      }

    })
    //获取再办个数
    this.providers.lightHttp.get("Flow/GetDoingFlowCount").then(data => {
      if (data > 0) {
        this.Doingflag = true;
        this.showDingIconText = data;
      } else {
        this.Doingflag = false;
      }

    })

  }

  ionViewDidEnter() {

    this.providers.lightHttp.get("Flow/GetToDoCount").then(data => {
      console.log(data);
      if (data > 0) {
        this.Todoflag = true;
        this.showTodoIconText = data;
      } else {
        this.Todoflag = false;
      }

    })
    //获取再办个数
    this.providers.lightHttp.get("Flow/GetDoingFlowCount").then(data => {
      if (data > 0) {
        this.Doingflag = true;
        this.showDingIconText = data;
      } else {
        this.Doingflag = false;
      }

    })

  }

  todoProcess() {
    let tabsctrl = TabsServices.getInstance();
    let tab = tabsctrl.getByIndex(1);
    TabsServices.setTabParams('todoProcess');
    tabsctrl.select(tab);
  }

  DoingFlow() {
    let tabsctrl = TabsServices.getInstance();
    let tab = tabsctrl.getByIndex(1);
    TabsServices.setTabParams('processing');
    tabsctrl.select(tab);
  }

  CreateFlow() {
    this.navCtrl.push(WorkNewProcessListPage);
  }

  newList() {
    this.navCtrl.push(NewListPage);
  }
  noticeList() {
    this.navCtrl.push(NoticeListPage);
  }
  emailList() {
    this.navCtrl.push(EmailListPage);
  }
  messageList() {
    this.navCtrl.push(MessageListPage);
  }

  /**
   *
   * 资产查询页面
   * @memberof HomePage
   */
  equipmentList() {
    this.navCtrl.push(EquipmentListPage);
  }

  /**
   *获取微信返回到当前页面时，需要还原的页面参数
   *
   * @private
   * @returns
   * @memberof HomePage
   */
  private getTabData() {
    let tabData = {
      tabIndex: 1,
    };
    return tabData;
  }


  /**
     * 用户退出
     */
  exitApp(): void {
    this.providers.lightExit.appExit();
  }

}



