import { Component, OnInit, Input, OnChanges, SimpleChanges, ViewChild } from '@angular/core';
import { Refresher, InfiniteScroll, Content } from 'ionic-angular';
import { LightBrowserUtil, LightArrayUtil, LightJsonUtil } from '../../light-util/light-util.module';
import { LightHttp } from '../../light-providers/light-providers.module';

@Component({
    selector: 'light-list',
    templateUrl: 'light-list.component.html'
})
export class LightListComponent implements OnInit, OnChanges {

    //标题的名称
    @Input() titleName: string = "";

    //题目的信息
    @Input() dataList: any = [];

    //数据的链接
    @Input() dataUrl: string = "";

    //数据的参数
    @Input() dataParam: any = {};

    //数据的链接
    @Input() idCol: string = "";

    @ViewChild(Content) content: Content;

    //尺寸信息
    limit: number = 10;

    //偏移量
    offset: number = 0;

    //行的总数
    rowCount: number = 0;

    //加载的
    loadFlag: boolean = true;

    //空的信息
    emptyFlag: boolean = false;

    infiniteFlag: boolean = false;

    keepScroll: any = null;

    isDoInfinite: boolean = false;

    listTotal: any;

    // 每次请求前更新，防止渲染覆盖
    typeSymbol: symbol;

    /**
     * 展现的标识
     * @type {boolean}
     * @memberof LightContentComponent
     */
    showFlag: boolean = true;

    //构造函数初始化
    constructor(
        public lightHttp: LightHttp
    ) {

    }

    ngOnInit(): void {

    }

    ngOnChanges(changes: SimpleChanges): void {
        LightArrayUtil.clear(this.dataList);
        this.infiniteFlag = false;
        this.loadFlag = true;
        this.emptyFlag = false;
        if (!!this.keepScroll) {
            this.keepScroll.enable(true);
        }
        if (!!!this.idCol) {
            this.idCol = "ObjectID";
        }
        if (!!this.dataUrl) {
            this._loadList(this.offset, this.limit);
        }
        if (LightBrowserUtil.isWeixinBrowser()) {
            this.showFlag = false;
        }
    }

    /**
     * 加载列表
     * @param {Number} 偏移
     * @param {Number} 尺寸
     *
     * @memberof MessageList
     */
    private _loadList(offset: number, limit: number, flag: boolean = true): Promise<any> {
        var that = this;
        var result: Promise<any> = new Promise(function (resolve, reject) {
            var urlParams: any = {};
            if (!!that.dataParam) {
                for (var key in that.dataParam) {
                    if (that.dataParam.hasOwnProperty(key)) {
                        urlParams[key] = that.dataParam[key];
                    }
                }
            }
            urlParams["offset"] = offset; urlParams["limit"] = limit;
            let typeSymbol = Symbol();
            that.typeSymbol = typeSymbol;
            that.lightHttp.get(that.dataUrl, urlParams).then(
                value => {
                    // 如果不是当前参数对应的回调数据，则不进行更新
                    if(typeSymbol != that.typeSymbol) return;
                    var objectTemp = LightJsonUtil.toObject(value);
                    var objectList: Array<any> = objectTemp['rows'];

                    for (var index = 0; index < objectList.length; index++) {
                        var element = objectList[index];
                        if (!that._containData(element[that.idCol])) {
                            if (flag) {
                                that.dataList.push(element);
                            } else {
                                that.dataList.push(element);
                            };
                        }
                    }

                    that.rowCount = objectList.length;

                    if (objectTemp["total"]) {
                        that.isDoInfinite = false;
                        that.listTotal = objectTemp["total"];
                    }
                    if (objectTemp["total"] == 0) {
                        that.emptyFlag = true;
                        that.loadFlag = false;
                        that.infiniteFlag = false;
                    }
                    if (objectList.length > 0) {
                        that.loadFlag = false;
                        that.emptyFlag = false;
                    }

                    resolve();
                }
            ).catch(errText => {
                reject();
            });
        }).catch(err => {
            console.log(err);
            that.emptyFlag = true;
            that.loadFlag = false;
            that.infiniteFlag = false;
        })
        return result;
    }

    /**
     * 判断数据是否存在
     * @private
     * @param {string} ID的标识信息
     * @returns {boolean}
     *
     * @memberof MessageList
     */
    private _containData(id: string): boolean {
        var result: boolean = false;
        if (!!id) {
            for (var index = 0; index < this.dataList.length; index++) {
                var element = this.dataList[index];
                if (element[this.idCol] == id) {
                    result = true;
                }
            }
        }
        return result;
    }

    //上拉刷新
    doRefresh(refresher: Refresher) {
        this.infiniteFlag = false;
        if (!!this.keepScroll) {
            this.keepScroll.enable(true);
        }
        LightArrayUtil.clear(this.dataList);
        if (!!this.dataUrl) {
            this._loadList(this.offset, this.limit).then(value => {
                refresher.complete();

            }).catch(value => {
                refresher.complete();
            });
        } else {
            setTimeout(() => {
                refresher.complete();
            }, 500);

        }
    }

    //下拉刷新
    doInfinite(infiniteScroll: InfiniteScroll): void {
        if (!!this.dataUrl) {
            if (this.dataList.length == this.listTotal) {
                infiniteScroll.enable(false);
                this.keepScroll = infiniteScroll;
                this.infiniteFlag = true;
                return
            }
            this._loadList(this.dataList.length, this.limit, false).then(value => {
                infiniteScroll.complete();
                if (this.rowCount < this.limit) {
                    infiniteScroll.enable(false);
                    this.keepScroll = infiniteScroll;
                    this.infiniteFlag = true;
                }
            }).catch(value => {
                infiniteScroll.complete();
            });
        } else {
            setTimeout(() => {
                infiniteScroll.complete();
            }, 1000);
        }
    }

}



