import { Component } from "@angular/core";
import { IonicPage, NavController, NavParams } from "ionic-angular";
import { Http } from "@angular/http";

/**
 * Generated class for the SearchDetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-search-detail",
  templateUrl: "search-detail.html"
})
export class SearchDetailPage {

  item: any;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public http: Http
  ) {}

  ionViewDidLoad() {
    console.log("ionViewDidLoad SearchDetailPage");
  }

  public ngOnInit(): void {
    this.http
      .get("http://" + location.host + "/assets/json/searchData.json")
      .subscribe(res => {
        this.item = res.json().find(item => {
          return item.CODE == this.navParams.get("CODE");
        });
      });
  }
}



