import { Component } from '@angular/core';
import { IonicPage, NavController,ViewController } from 'ionic-angular';
import { LightOutModule,LightHttp,LightPop,LightJsonUtil } from '../../../light/light-out/light-out.module';
import { EquipmentDetailPage } from '../equipment-detail/equipment-detail';

@IonicPage()
@Component({
  selector: 'page-enter-manually',
  templateUrl: 'enter-manually.html',
})
export class EnterManuallyPage {

  code:string="130151E020020";

  constructor(public navCtrl: NavController, public viewCtrl: ViewController, public lightHttp:LightHttp,public lightPop:LightPop) {
  }

  //关闭模态窗口
  dismiss(){
    this.viewCtrl.dismiss();
  }

  toEquipment(){
    if(!!this.code){
      this.lightHttp.get("Equipment/GetEquipmentCode",{code:this.code}).then(result=>{
        var resultObject=LightJsonUtil.toObject(result);
        this.navCtrl.push(EquipmentDetailPage,{code:resultObject["CODE"],id:this.code});
        resultObject=null;
      }).catch(errText=>{
        this.lightPop.message("未查到该编码设备!");
      });
    }else{
      this.lightPop.message("输入不能为空");
    }

  }

}



