import { Component } from "@angular/core";
import { IonicPage, NavController, NavParams } from "ionic-angular";
import { LightPop } from "../../../light/light-providers/providers";
import { Http } from "@angular/http";

/**
 * Generated class for the LogDetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-log-detail",
  templateUrl: "log-detail.html"
})
export class LogDetailPage {
  // 后续考虑开发的日历交互型日期相关字段
  showStatus = true;
  calendar = {
    currentDate: 3,
    daysInLastMonth: [29, 30, 31],
    daysInThisMonth: [1, 2, 3, 4, 5, 6, 7, 8],
    daysInNextMonth: [29, 30, 31]
  };

  // 默认日期
  selectDay = "2019-03-21";

  // 伪造的项目模板，实际请求逻辑待后台沟通确认，此处只表明约束关系
  logTemplate = [
    {
      itemName: "咸宁",
      itemNumber: "11111",
      itemTime: "",
      itemContent: "",
      extendFields: []
    },
    {
      itemName: "泉州",
      itemNumber: "22222",
      itemTime: "",
      itemContent: "",
      extendFields: []
    },
    {
      itemName: "贵阳",
      itemNumber: "33333",
      itemTime: "",
      itemContent: "",
      extendFields: []
    }
  ];

  // 项目列表
  itemList = [];

  // 浏览器标题
  titleName = "";

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public lightPop: LightPop,
    public http: Http
  ) {}

  ionViewDidLoad() {
    console.log("ionViewDidLoad LogDetailPage");
  }

  public ngOnInit(): void {
    if (this.navParams.get("CODE")) {
      // 编辑态
      this.titleName = "编辑日志";
      this.http
        .get("http://" + location.host + "/assets/json/log/itemList.json")
        .subscribe(res => {
          this.itemList = res.json();
        });
    } else {
      // 新建态
      this.titleName = "新建日志";
      this.addItem(true);
    }
  }

  /**
   *添加新项目
   *
   * @param {boolean} [withItemName=false] 是否预设项目名称
   * @memberof LogDetailPage
   */
  public addItem(withItemName = false) {
    // 此处的空项目结构，应该在初始化时构建。项目名来源可以是历史，也可以是模板源的第一个
    let item = [
      {
        fieldName: "itemName",
        fieldLabel: "项目名称",
        fieldType: "select",
        fieldValue: ""
      },
      {
        fieldName: "itemNumber",
        fieldLabel: "项目编号",
        fieldType: "select",
        fieldValue: ""
      },
      {
        fieldName: "itemTime",
        fieldLabel: "工时",
        fieldType: "text",
        fieldValue: ""
      },
      {
        fieldName: "itemContent",
        fieldLabel: "工作内容",
        fieldType: "text",
        fieldValue: ""
      }
    ];
    this.itemList.push(item);
    if (withItemName) {
      setTimeout(() => {
        this.itemList[
          this.itemList.length - 1
        ][0].fieldValue = this.logTemplate[0].itemName;
      }, 100);
    }
  }

  /**
   *删除项目
   *
   * @param {*} index 项目索引
   * @param {*} slidingItem 红色删除按钮引用，用于帮用户滑回正常状态
   * @memberof LogDetailPage
   */
  public deleteItem(index, slidingItem) {
    if (this.itemList.length == 1) {
      this.lightPop
        .message("至少存在一个项目")
        .then(result => {
          slidingItem.close();
        })
        .catch(errText => {});
    } else {
      this.lightPop
        .confirm("确定删除吗？")
        .then(result => {
          this.itemList.splice(index, 1);
        })
        .catch(errText => {
          slidingItem.close();
        });
    }
  }

  /**
   *字段规则
   *
   * @param {string} fieldName 字段名
   * @param {number} index 项目索引
   * @param {*} logFieldValues 不定量的字段参数，扩展多字段联动时用
   * @memberof LogDetailPage
   */
  public logRule(fieldName: string, index: number, ...logFieldValues) {
    switch (fieldName) {
      // 项目名称：关联项目编号、扩展字段
      case "itemName":
        ((index, logFieldValues) => {
          let [itemName, itemTime] = logFieldValues;
          this.itemList[index].map(log => {
            switch (log.fieldName) {
              case "itemNumber":
                log.fieldValue = this.logTemplate.find(log => {
                  return log.itemName == itemName;
                }).itemNumber;
                break;
              default:
                break;
            }
          });
        })(index, logFieldValues);
        break;
      // 项目编号：关联项目名称
      case "itemNumber":
        ((index, logFieldValues) => {
          let [itemNumber] = logFieldValues;
          this.itemList[index].map(log => {
            switch (log.fieldName) {
              case "itemName":
                log.fieldValue = this.logTemplate.find(log => {
                  return log.itemNumber == itemNumber;
                }).itemName;
                break;
              default:
                break;
            }
          });
        })(index, logFieldValues);
        break;
      default:
        break;
    }
  }

  /**
   *获取日志中某字段名对应的值
   *
   * @param {*} log 日志对象
   * @param {*} fieldName 字段名
   * @returns
   * @memberof LogDetailPage
   */
  public getFieldValue(log: any, fieldName) {
    return log.find(field => {
      return field.fieldName == fieldName;
    }).fieldValue;
  }

  /**
   *提交日志，后续追加后台请求再进行弹框
   *
   * @memberof LogDetailPage
   */
  public submitLog() {
    this.lightPop
      .message(this.navParams.get("CODE") ? "编辑成功" : "提交成功", "", false)
      .then(result => {
        this.navCtrl.pop();
      })
      .catch(errText => {});
  }

  // public changeStatus() {
  //   this.showStatus = !this.showStatus;
  // }

  // 日历
  // public getEventsOn(day) {
  //   return [
  //     {
  //       color: "red"
  //     }
  //   ];
  // }
}



