import { LightPlatform } from './../../light-providers/providers/lightPlatform';
import { Component } from '@angular/core';
import { NavController, ActionSheetController, IonicPage } from 'ionic-angular';
import { LightImagePicker, LightCamera } from './../../light-nation/light-nation.index';
import { LightUser, LightPop } from '../../light-providers/light-providers.module';
import { normalizeURL } from 'ionic-angular';
import { EmailModifyComponent } from './email-modify/email-modify.component';
import { PasswordModifyComponent } from './password-modify/password-modify.component';

@IonicPage()
@Component({
    selector: 'light-person',
    templateUrl: 'light-person.component.html'
})
export class LightPersonComponent {

    //用户登录的名称
    userLoginName: string;

    //用户的名称
    userName: string;

    //用户的邮箱地址
    userEmailAdress: string;

    //用户的手机号码
    userMobile: string;

    //用户的座机
    userOffcieTel: string;

    //默认头像
    memberPicture: string = "assets/img/no-user.png";

    /**
     * light用户的组件对象
     * @param {NavController} navCtrl
     * @param {LightUser} lightUser
     * @memberof LightPersonComponent
     */
    constructor(
        public navCtrl: NavController,
        public actionSheetCtrl: ActionSheetController,
        public lightPop: LightPop,
        public lightCamera: LightCamera,
        public lightImagePicker: LightImagePicker,
        public lightUser: LightUser,
        public lightPlatform: LightPlatform
    ) {

    }

    /**
     * 进入加载模式
     *
     * @memberof LightPersonComponent
     */
    ionViewWillEnter(): void {
        var userInfo = this.lightUser.getUserInfo();
        if (!!userInfo) {
            this.userLoginName = userInfo.LoginName;
            this.userName = userInfo.UserName;
            this.userEmailAdress = userInfo.Email;
            this.userMobile = userInfo.Mobile;
            this.userOffcieTel = userInfo.OffcieTel;
        }
    }

    //修改邮箱
    upstateEmailInfo() {
        this.navCtrl.push(EmailModifyComponent);
    }

    //修改密码
    changePassword() {
        this.navCtrl.push(PasswordModifyComponent);
    }

    changePhoto() {
        let actionSheet = this.actionSheetCtrl.create({
            title: '个人头像',
            buttons: [{
                text: '拍照',
                role: 'takePicture',
                handler: () => {
                    this.getPicture();
                }
            }, {
                text: '相册',
                role: 'imagePicker',
                handler: () => {
                    this.getImage();
                }
            }, {
                text: '取消',
                role: 'cancel',
                handler: () => {
                    console.log("取消");
                }
            }]
        });
        actionSheet.present();
    }

    /**
     *调用相机
     *
     * @memberof LightPersonComponent
     */
    getPicture() {
        this.lightCamera.getPicture().then((data) => {
            this.memberPicture = this.convertFileSrc(data);
            console.log(data)
        }).catch(errText => {
            console.log(errText)
        })
    }

    /**
     *调用相册
     *
     * @memberof LightPersonComponent
     */
    getImage() {
        this.lightImagePicker.getPicture(
            null,
            {maximumImagesCount: 1}
        ).then(results => {
            console.log(results)
            this.memberPicture = this.convertFileSrc(results[0]);
        }).catch(errText => {
            console.log(errText)
        })
    }

    /**
     *处理相机或相册插件返回的图片路径跨域问题
     *
     * @param {*} imgSrc
     * @returns
     * @memberof LightPersonComponent
     */
    convertFileSrc(imgSrc: any) {
        if(this.lightPlatform.isCordova()) {
            // return window['Ionic'].WebView.convertFileSrc(imgSrc);
            return normalizeURL(imgSrc);
        } else {
            return imgSrc;
        }
    }

}



