import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { LightBrowserUtil } from '../../light-util/light-util.module';
import { Content } from 'ionic-angular';

@Component({
  selector: 'light-content',
  templateUrl: 'light-content.html'
})
export class LightContentComponent implements OnInit {

  @ViewChild(Content) content: Content;

  ///标题的名称
  @Input() titleName: string = "";

  // 工作详情页使用的独立布局写法
  @Input() useScroll = false;

  // 滚动时，通过调用失去焦点方法，关闭软键盘
  @Input() scrollHideKeyboard = false;

  /**
   * 展现的标识
   * @type {boolean}
   * @memberof LightContentComponent
   */
  showFlag: boolean = true;

  constructor(

  ) {

  }

  /**
   * 组件初始化
   * @memberof LightContentComponent
   */
  ngOnInit(): void {
    if (LightBrowserUtil.isWeixinBrowser()) {
      this.showFlag = false;
      document.getElementsByTagName('title')[0].innerHTML = this.titleName;
    }
  }

  onScrollStart(event: any) {
    if (this.scrollHideKeyboard) {
      if (event === null) return;
      // 关闭软键盘
      let activeElement = <HTMLElement>document.activeElement;
      activeElement && activeElement.blur && activeElement.blur();
    }
  }

}



