import { LightJsonUtil, LightLoading, LightPop, LightFlowService, LightWriterFlow, LightNav } from './../../../light/light-out/light-out.module';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

@IonicPage()
@Component({
  selector: 'page-work-new-process',
  templateUrl: 'work-new-process.html',
})
export class WorkNewProcessPage {

  /**
   * 文档的ID
   * @type {*}
   * @memberof WorkNewProcessPage
   */
  sCode: any;

  /**
   * 写入的流程信息
   *
   * @type {LightWriterFlow}
   * @memberof WorkNewProcessPage
   */
  writerFlow: LightWriterFlow;

  /**
   * 写入的数据信息
   *
   * @type {*}
   * @memberof WorkNewProcessPage
   */
  writeData: any;

  /**
   * 创建一个新的流程信息初始化
   * @param {NavController} navCtrl
   * @param {NavParams} navParams
   * @param {LightFlowService} flowServcie
   * @memberof WorkNewProcessPage
   */
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public lightload: LightLoading,
    public flowServcie: LightFlowService,
    public lightpop: LightPop,
    public lightNav: LightNav
  ) {
    this.sCode = this.navParams.data.item.Code;
  }

  ionViewDidLoad() {
    //加载进度条
    let loadctrl = this.lightload.showLoading('正在加载中...');
    this.flowServcie.getStartFlow(this.sCode).then(value => {
      this.writerFlow = value;
      this.flowServcie.getWriterForm(this.writerFlow).then(result => {
        //结束进度条,避免超时
        loadctrl.instance && loadctrl.dismiss();
        this.writeData = LightJsonUtil.toObject(result);
      }).catch(err=>{
        loadctrl.instance && loadctrl.dismiss();
        this.lightpop.errMessage(err);
        this.navCtrl.pop();
      });
    }).catch(err=>{
        loadctrl.instance && loadctrl.dismiss();
        this.navCtrl.pop();
    });
  }

  /**
   * 提交任务的信息
   *
   * @memberof WorkNewProcessPage
   */
  submitProcess() {
    this.flowServcie.submitFlow(this.navCtrl, this.writerFlow, this.writeData);
  }

}



