import { Component, ViewChild, OnInit } from "@angular/core";
import { NavController, Keyboard } from "ionic-angular";
import { LightStringUtil } from "../../light/light-util/util";
import { Http } from "@angular/http";
import { LightConfig } from "../../light/light-providers/providers";
import { SearchDetailPage } from "./search-detail/search-detail";

/**
 * Generated class for the SearchPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: "page-search",
  templateUrl: "search.html"
})
export class SearchPage implements OnInit {
  @ViewChild("inputSearch") inputSearch;

  // 搜索结果列表
  datalist = [];

  // 搜索历史
  searchHistory = {
    show: false,
    data: []
  };

  // 搜索提示
  searchTip = {
    show: false,
    data: []
  };

  // 搜索输入字符串
  searchVal = "";

  // 搜索类型
  searchSeg = "0";

  constructor(
    public navCtrl: NavController,
    public http: Http,
    public keyboard: Keyboard,
    public lightConfig: LightConfig
  ) {}

  public ngOnInit() {
    // 从本地缓存取出搜索历史
    let searchHistory = JSON.parse(localStorage.getItem("searchHistory"));
    if (Array.isArray(searchHistory)) {
      this.searchHistory.data = searchHistory;
    }
    // 默认按全部搜索
    this.search("");
  }

  /**
   *搜索框获得焦点时，显示便捷输入列表
   *
   * @memberof SearchPage
   */
  public searchFocus() {
    if (this.searchVal.trim() == "") {
      // 显示搜索历史
      this.searchHistory.show = true;
      this.searchTip.show = false;
    } else {
      // 显示搜索关键字提示
      this.http
        .get("http://" + location.host + "/assets/json/searchData.json")
        .subscribe(res => {
          this.searchTip.data = res.json().filter(item => {
            console.log(this.searchSeg);
            return (
              item.Title.toLowerCase().indexOf(this.searchVal.toLowerCase()) >
                -1 && item.type == this.searchSeg
            );
          });
          this.searchTip.show = true;
          this.searchHistory.show = false;
        });
    }
  }

  /**
   *搜索框失去焦点时，延时隐藏便捷输入列表，防止与便捷输入的点击事件冲突
   *
   * @memberof SearchPage
   */
  public searchBlur() {
    setTimeout(() => {
      this.searchHistory.show = false;
      this.searchTip.show = false;
    }, 100);
  }

  /**
   *清除搜索框
   *
   * @memberof SearchPage
   */
  public searchClear() {
    this.searchVal = "";
    this.searchHistory.show = true;
    this.searchTip.show = false;
  }

  /**
   *删除搜索历史
   *
   * @param {*} e
   * @param {number} index
   * @memberof SearchPage
   */
  public deleteHistory(e: any, index: number) {
    e.preventDefault();
    e.stopPropagation();
    this.searchHistory.data.splice(index, 1);
    localStorage.setItem(
      "searchHistory",
      JSON.stringify(this.searchHistory.data)
    );
  }

  /**
   *监听键盘按键
   *
   * @param {*} e
   * @memberof SearchPage
   */
  public getItems(e: KeyboardEvent) {
    // 便捷列表显隐逻辑
    if (this.searchVal.trim() == "") {
      // 输入框为空时，显示搜索历史
      this.searchHistory.show = true;
      this.searchTip.show = false;
    } else {
      // 输入框有值时，显示搜索提示
      this.searchHistory.show = false;
      this.http
        .get("http://" + location.host + "/assets/json/searchData.json")
        .subscribe(res => {
          this.searchTip.data = res.json().filter(item => {
            return (
              item.Title.toLowerCase().indexOf(this.searchVal.toLowerCase()) >
                -1 && item.type == this.searchSeg
            );
          });
          this.searchTip.show = true;
        });
    }
    // 对应键盘的回车或软键盘的搜索
    let key = e.key || e.keyCode;
    if (key === "Enter" || key === 13) {
      const val = this.searchVal;
      this.search(val.trim());
      // 关闭软键盘
      let activeElement = <HTMLElement>document.activeElement;
      activeElement && activeElement.blur && activeElement.blur();
    }
  }

  /**
   *调用搜索接口
   *
   * @param {*} val
   * @memberof SearchPage
   */
  public search(val) {
    this.http
      .get("http://" + location.host + "/assets/json/searchData.json")
      .subscribe(res => {
        this.datalist = res.json().filter(item => {
          return (
            item.Title.toLowerCase().indexOf(val.toLowerCase()) > -1 &&
            item.type == this.searchSeg
          );
        });
      });
    if (val.trim() != "") {
      if (this.searchHistory.data.length == 5) {
        this.searchHistory.data.pop();
      }
      this.searchHistory.data.unshift(val.trim());
      localStorage.setItem(
        "searchHistory",
        JSON.stringify(this.searchHistory.data)
      );
    }
  }

  ionViewWillLeave() {
    this.lightConfig.baseUrl = "http://172.16.50.135:27116/"; //城堪院内网
  }

  /**
   *通过点击搜索框下方列表，便捷搜索
   *
   * @param {*} val
   * @memberof SearchPage
   */
  public quickSearch(val) {
    this.searchVal = val;
    this.search(val);
  }

  /**
   *临时用样式
   *
   * @param {*} row
   * @returns {string}
   * @memberof SearchPage
   */
  public titleCheck(row: any): string {
    var result = row.Title;
    if (!!!result) {
      result = row.JobType;
    }
    return result;
  }

  /**
   *临时用样式
   *
   * @param {*} input
   * @returns {*}
   * @memberof SearchPage
   */
  public flowImage(input: any): any {
    var out = "";
    if (LightStringUtil.contain(input, "公文办理")) {
      out = "icon1";
    } else if (LightStringUtil.contain(input, "公章")) {
      out = "icon3";
    } else if (LightStringUtil.contain(input, "车辆使用申请单")) {
      out = "icon20";
    } else if (LightStringUtil.contain(input, "请假")) {
      out = "icon5";
    } else if (LightStringUtil.contain(input, "软硬件采购")) {
      out = "icon15";
    } else if (LightStringUtil.contain(input, "发文登记")) {
      out = "icon23";
    } else if (LightStringUtil.contain(input, "来文登记")) {
      out = "icon24";
    } else {
      out = "icon1";
    }
    out = "assets/img/icon-work/" + out + ".png";
    return out;
  }

  public toDetail(CODE) {
    this.navCtrl.push(SearchDetailPage, { CODE });
  }
}



