import { Injectable } from "@angular/core";
import { Diagnostic } from "@ionic-native/diagnostic";
import { Observable } from "rxjs/Observable";
import { Platform, AlertController } from "ionic-angular";
import { LatLng, WebSpatialReference } from "./LatlngUtil";

declare var baidumap_location: any; //百度定位模块

/**
 * 本地npm的安装: npm install --save @ionic-native/diagnostic;
 * 加载ionic插件:ionic cordova plugin add cordova.plugins.diagnostic;
 * 加载ionic插件:ionic cordova plugin add cordova-plugin-baidumaplocation@3.2.0 --variable ANDROID_KEY="dcbZsdvqalbBTDEE03GoZro3CwNgNGO0" --variable IOS_KEY="skm1oP6HDZW33atFknc0dSIf0MnFellH"
 * 加载ionic插件:ionic cordova plugin add cordova-plugin-baidumaplocation --variable ANDROID_KEY="dcbZsdvqalbBTDEE03GoZro3CwNgNGO0" --variable IOS_KEY="skm1oP6HDZW33atFknc0dSIf0MnFellH"
 * 平台:Android | iOS
 * 百度地图
 * @memberof LightLocationService
 */
@Injectable()
export class LightLocationService {
  private enabledLocationService = false; //手机是否开启位置服务

  private locationAuthorization = false; //手机是否有定位权限

  constructor(
    private diagnostic: Diagnostic,
    private platform: Platform,
    private alertCtrl: AlertController
  ) {
    console.log("Hello LightLocationServiceProvider Provider");
  }

  /**
   * 是否是真机环境
   *
   * @returns {boolean}
   * @memberof LightLocationService
   */
  public isMobile(): boolean {
    return this.platform.is("mobile") && !this.platform.is("mobileweb");
  }

  /**
   * 获得用户当前坐标
   */
  public getUserLocation(): Observable<any> {
    return Observable.create(observer => {
      if (this.isMobile()) {
        this.assertLocationService().subscribe(
          res => {
            if (res) {
              this.assertLocationAuthorization().subscribe(
                res => {
                  if (res) {
                    return this.getLocation(observer);
                  }
                },
                err => {
                  observer.error(err);
                }
              );
            }
          },
          err => {
            observer.error(err);
          }
        );
      } else {
        console.log("非手机环境,即测试环境返回固定坐标");
        observer.next(new LatLng(41.786438059549, 123.362592840223));
      }
    });
  }

  /**
   * 获取位置信息
   *
   * @private
   * @param {any} observer
   * @memberof LightLocationService
   */
  private getLocation(observer: any) {
    baidumap_location &&
      baidumap_location.getCurrentPosition(
        data => {
          observer.next(
            new LatLng(
              data.latitude,
              data.longitude,
              WebSpatialReference.BD_09
            ).toWGS_84()
          );
        },
        errMsg => {
          if (errMsg.code === errMsg.PERMISSION_DENIED) {
            this.openRequestPremission();
          } else if (errMsg.code === errMsg.POSITION_UNAVAILABLE) {
            alert("定位失败,请确保连上WIFI或者关掉WIFI只开流量数据");
          } else if (errMsg.code === errMsg.TIMEOUT) {
            alert("网络连接异常,请检查您的网络是否畅通");
          }
          observer.error("获取位置失败");
        }
      );
  }

  /**
   * 打开请求权限的对话框
   *
   * @private
   * @memberof LightLocationService
   */
  private openRequestPremission(): void {
    this.alertCtrl
      .create({
        title: "缺少定位权限",
        subTitle: "请在手机设置或app权限管理中开启",
        buttons: [
          {
            text: "取消"
          },
          {
            text: "去开启",
            handler: () => {
              this.diagnostic.switchToSettings();
            }
          }
        ]
      })
      .present();
  }

  //检测app位置服务是否开启
  private assertLocationService = (() => {
    return () => {
      return Observable.create(observer => {
        if (this.enabledLocationService) {
          observer.next(true);
        } else {
          this.diagnostic
            .isLocationEnabled()
            .then(enabled => {
              if (enabled) {
                this.enabledLocationService = true;
                observer.next(true);
              } else {
                this.enabledLocationService = false;
                this.alertCtrl
                  .create({
                    title: "您未开启位置服务",
                    subTitle: "正在获取位置信息",
                    buttons: [
                      { text: "取消" },
                      {
                        text: "去开启",
                        handler: () => {
                          this.diagnostic.switchToLocationSettings();
                        }
                      }
                    ]
                  })
                  .present();
                observer.error(false);
              }
            })
            .catch(err => {
              console.warn(err, "调用diagnostic.isLocationEnabled方法失败");
              observer.error(false);
            });
        }
      });
    };
  })();

  //检测app是否有定位权限
  private assertLocationAuthorization = (() => {
    return () => {
      return Observable.create(observer => {
        if (this.locationAuthorization) {
          observer.next(true);
        } else {
          this.diagnostic
            .isLocationAuthorized()
            .then(res => {
              if (res) {
                this.locationAuthorization = true;
                observer.next(true);
              } else {
                this.locationAuthorization = false;
                this.diagnostic
                  .requestLocationAuthorization("always")
                  .then(res => {
                    //请求定位权限
                    if (res == "DENIED_ALWAYS") {
                      //拒绝访问状态,必须手动开启
                      this.locationAuthorization = false;
                      this.alertCtrl
                        .create({
                          title: "缺少定位权限",
                          subTitle: "请在手机设置或app权限管理中开启",
                          buttons: [
                            { text: "取消" },
                            {
                              text: "去开启",
                              handler: () => {
                                this.diagnostic.switchToSettings();
                              }
                            }
                          ]
                        })
                        .present();
                      observer.error(false);
                    } else {
                      this.locationAuthorization = true;
                      observer.next(true);
                    }
                  })
                  .catch(err => {
                    console.warn(
                      err,
                      "调用diagnostic.requestLocationAuthorization方法失败"
                    );
                    observer.error(false);
                  });
              }
            })
            .catch(err => {
              console.warn(err, "调用diagnostic.isLocationAvailable方法失败");
              observer.error(false);
            });
        }
      });
    };
  })();
}



