import { Component, OnInit, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, Slides } from 'ionic-angular';
import { LightHttp, LightJsonUtil, LightUser, LightLocalCache, LightFileTransfer, LightPop,LightFile, LightNav  } from './../../../light/light-out/light-out.module';
import { EquipmentEditPage } from './../equipment-edit/equipment-edit';

/**
 * Generated class for the EquipmentDetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-equipment-detail',
  templateUrl: 'equipment-detail.html',
})
export class EquipmentDetailPage {

  ///读取的数据信息
  readData: any;

  ///图片的字节数组
  imageArray: any;

  ///对应的Code的值信息
  code: string;

  ///编辑的标识
  editFlag: boolean = false;

  ///图片编码的Url对象
  imageCodeUrl: string = "";

  ///打印的标识
  printFlag: boolean = false;

  ///设置的ID信息
  id: string = "";

  ///根目录的路径信息
  rootFilePath="1_YeJing";

  // 图片缩放

  @ViewChild('mySlides') mySlides: Slides;

  // 轮播图的下标
  indexsrc: any;
  // 第一次点击时间
  temp: any;
  // 第二次点击时间
  tempTime: any;
  // 单击事件倒计时
  timeout: any;
  // 是否放大
  isZoom: boolean = false;
  // 是否查看大图
  isWatchDetail: boolean = false;
  /**
   * 项目详细的页面对象
   * @param {NavController} navCtrl
   * @param {NavParams} navParams
   * @param {LightHttp} lightHttp
   * @memberof ProjectDetailPage
   */
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public lightHttp: LightHttp,
    public lightUser: LightUser,
    public localCache: LightLocalCache,
    public lightFileTransfer: LightFileTransfer,
    public lightPop: LightPop,
    public lightFile: LightFile,
    public lightNav: LightNav
  ) {
    let data = this.navParams.data;
    this.code = data.code;
    this.id = data.id;
    lightFile.setRootFilePathName(this.rootFilePath);
  }

  ///内部初始化
  public ngOnInit(): void {
    // this._editFlagInit();
    // this._readDataInit();
    // this._imageDataInit();
  }

  ionViewWillEnter(): void {
    this._editFlagInit();
    this._readDataInit();
    this._imageDataInit();
    this._imageUrlInit();
  }

  ///数据的初始化
  private _readDataInit(): void {
    this.lightHttp.get("Equipment/GetCodeForm", { editFlag: false, code: this.code, sUserName: this.lightUser.getUserName() }).then((value) => {
      this.readData = LightJsonUtil.toObject(value);
    }).catch(() => {
    });
  }

  ///图片的数据初始化
  private _imageDataInit(): void {
    this.lightHttp.get("Equipment/GetCodePictures", { code: this.code, sUserName: this.lightUser.getUserName() }).then((value) => {
      this.imageArray = LightJsonUtil.toObject(value);
    }).catch(() => {
    });
  }

  ///图片编辑权限的初始化
  private _editFlagInit(): void {
    this.lightHttp.get("Equipment/GetCodePermissions", { code: this.code, sUserName: this.lightUser.getUserName() }).then((value) => {
      var resultObject = LightJsonUtil.toObject(value);
      if (!!resultObject["Type"] && resultObject["Type"] != "VIEW") {
        this.editFlag = true;
      }
    }).catch(() => {
    });
  }

  /**
   * 图片的url的初始化
   * @private
   * @memberof EquipmentDetailComponent
   */
  private _imageUrlInit(): void {
    var writerFlag = this.localCache.get("writerFlag");
    if (!!writerFlag && writerFlag == "true"&&!!this.id) {
      this.imageCodeUrl = this.lightHttp.getUrl("Equipment/GetQrCodePicture?code=" + this.code);
      this.printFlag = true;
      this.localCache.set("writerFlag", "false");
    }
  }

  /**
   * 进入编辑
   *
   * @private
   * @memberof EquipmentDetailComponent
   */
  _gotoEdit(): void {
    this.navCtrl.push(EquipmentEditPage, { code: this.code });
  }

  /**
   * 图片的打印
   *
   * @memberof EquipmentDetailComponent
   */
  _printPic(): void {
    var pictureName=this.id + ".jpg";
    ///下载本地的操作
    this.lightFileTransfer.download(this.lightHttp.getUrl("Equipment/GetQrCodePicture?code=" + this.code),pictureName ).then(result => {
      this.lightPop.message("二维码下载成功，保存到"+this.rootFilePath+"目录下"+pictureName,null,false);
    }).catch(errText => {
      this.lightPop.message("保存失败" + LightJsonUtil.toJson(errText));
    });
  }

  //单击显示图片滑动页
  showImage(ev: any, indexsrc) {
    this.indexsrc = indexsrc;
    let imgScale = ev.target;
    imgScale.style.transition = 'transform 0.33s';
    this.isWatchDetail = !this.isWatchDetail;
    // this.initSlides();
  }
  initSlides() {
    this.mySlides.initialSlide(this.indexsrc);
    // this.slides.slideTo(indexsrc,200);

  }
  //单击关闭图片滑动页，需要判断是不是双击
  doClick(ev: any) {
    clearTimeout(this.timeout);
    if (!this.temp) {
      this.temp = new Date();
    } else {
      this.tempTime = this.temp;
      this.temp = new Date();
    }
    //单击事件倒计时
    this.timeout = setTimeout(() => {
      this.slideDown(ev);
    }, 400);
    //两次点击时间差
    let clickBuffer = this.temp - this.tempTime;
    //双击事件触发
    if (clickBuffer && clickBuffer < 400) {
      this.doDoubleClick(ev);
      clearTimeout(this.timeout);
    }


  }
  //双击放大缩小
  doDoubleClick(ev: any) {
    //获取事件对象
    console.log("double");
    let imgScale = ev.target;
    imgScale.style.transition = 'transform 0.33s';

    if (!this.isZoom) {
      imgScale.style.transform = 'scale(2)';
    } else {
      imgScale.style.transform = 'scale(1)';
    }
    this.isZoom = !this.isZoom;
  }
  // 回退
  slideDown(ev: any) {
    let imgScale = ev.target;
    if (this.isZoom) {
      imgScale.style.transition = 'transform 0.33s';
      imgScale.style.transform = 'scale(1)';
    } else {
      setTimeout(() => {
        this.isWatchDetail = !this.isWatchDetail;
      }, 400);
    }
    this.isZoom = !this.isZoom;
  }
  // 取消查看图片
  closeImg() {
    this.isWatchDetail = !this.isWatchDetail;

  }
}



