import { LightHttp, LightJsonUtil, LightWriterService, LightNav } from './../../../light/light-out/light-out.module';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { WorkFlowChartPage } from '../work-flow-chart/work-flow-chart';

@IonicPage()
@Component({
  selector: 'page-work-detail',
  templateUrl: 'work-detail.html',
})
export class WorkDetailPage {

  // 对应的单项code值
  code: string;
  //流程类型
  JobType: string;
  //当前环节名称
  curStepTitle: string;
  //底部是否显示
  showFlag: boolean;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public lightHttp: LightHttp,
    public lightWriterService: LightWriterService,
    public lightNav: LightNav,
  ) {
    let item = this.navParams.data.item;
    this.code = item.CODE;
    this.JobType = item.JobType;
    this.showFlag = item.showFlag;
  }

  public ngOnInit(): void {
    this.lightHttp.get("Flow/GetCurStep", { objectID: this.code }).then((data) => {
      var curStepData = LightJsonUtil.toObject(data) as any;
      this.curStepTitle = (JSON.stringify(curStepData) === '{}') ? "已经归档" : curStepData.Title;
    }).catch(err=>{
      console.log(err);
    });
  }

  //流程日志
  lookFlow() {
    this.navCtrl.push(WorkFlowChartPage, { objectID: this.code })
  }

}



