import { Injectable } from "@angular/core";
import { Platform } from "ionic-angular";
import { LightNation } from "../common/lightNation";
import { FileTransfer, FileTransferObject } from "@ionic-native/file-transfer";
import { LightFile, LightFileType } from "../light-file/index";
import { LightStringUtil, MapList } from "../../light-util/light-util.module";

/**
 * 本地npm的安装:npm install --save @ionic-native/file-transfer
 * 加载ionic插件:ionic cordova plugin add cordova-plugin-file-transfer
 * 平台:Amazon Fire OS | Android | Browser | iOS | Ubuntu | Windows | Windows Phone
 * 文件的传输
 * @class lightFileTransfer
 */
@Injectable()
export class LightFileTransfer extends LightNation {
  /**
   * 下载的地图列表
   * @private
   * @type {MapList}
   * @memberof lightFileTransfer
   */
  private downMapList: MapList = null;

  /**
   * 上传文件的列表
   * @private
   * @type {MapList}
   * @memberof lightFileTransfer
   */
  private upLoadList: MapList = null;

  constructor(
    public fileTransfer: FileTransfer,
    public lightFile: LightFile,
    private platform: Platform
  ) {
    super();
    this.downMapList = new MapList();
    this.upLoadList = new MapList();
  }

  /**
   * 文件的上传
   *
   * @param {string} filePath
   * @param {string} upLoadUrl
   * @returns {Promise<any>}
   * @memberof lightFileTransfer
   */
  upLoad(
    filePath: string,
    upLoadUrl: string,
    listener: (event: ProgressEvent) => any = null,
    targetElement: any = null
  ): Promise<any> {
    var result: Promise<any> = null;
    if (!!filePath && !!upLoadUrl) {
      var that = this;
      result = this._getDefaultResult("");
      if (this.platform.is("cordova")) {
        result = new Promise(function(resolve, reject) {
          if (!that.upLoadList.containkey(filePath)) {
            var fileTransferObject = that.fileTransfer.create();
            that.upLoadList.put(filePath, fileTransferObject);
            if (!!listener) {
              fileTransferObject.onProgress(listener);
            }
            fileTransferObject
              .upload(filePath, upLoadUrl)
              .then(uploadResult => {
                that.upLoadList.remove(filePath);
                resolve({
                  message: uploadResult.response,
                  target: targetElement
                });
              })
              .catch(uploadErrText => {
                that.upLoadList.remove(filePath);
                reject(uploadErrText);
              });
          } else {
            reject("已经正在上传中");
          }
        });
      }
    }
    return result;
  }

  /**
   * 忽略上传的操作
   *
   * @memberof lightFileTransfer
   */
  abortUpload(url: string): void {
    if (!!url) {
      if (this.upLoadList.containkey(url)) {
        var fileTransferObject = this.upLoadList.getValue(
          url
        ) as FileTransferObject;
        if (!!fileTransferObject) {
          fileTransferObject.abort();
        }
        this.upLoadList.remove(url);
      }
    }
  }

  /**
   * 下载文件对象
   *
   * @param {string} 链接信息
   * @param {string} [fileName=null] 保存的文件名称
   * @param {LightFileType} [fileType=LightFileType.RootFile]  保存文件路径类型
   * @param {(event: ProgressEvent) => any} [listener=null] 监听的对象信息
   * @returns {Promise<any>}
   * @memberof LightFileTransfer
   */
  download(
    url: string,
    fileName: string = null,
    fileType: LightFileType = LightFileType.RootFile,
    listener: (event: ProgressEvent) => any = null
  ): Promise<any> {
    if (!!!fileName) {
      fileName = this._getFileExtentName(url);
    }
    var that = this;
    var result: Promise<any> = null;
    if (!!url) {
      result = this._getDefaultResult("");
      if (this.platform.is("cordova")) {
        var filePromise = that._getFilePath(fileType);
        result = new Promise(function(resolve, reject) {
          filePromise
            .then(path => {
              if (!that.downMapList.containkey(url)) {
                var fileTransferObject = that.fileTransfer.create();
                that.downMapList.put(url, fileTransferObject);
                if (!!listener) {
                  fileTransferObject.onProgress(listener);
                }
                //alert( path +" "+ fileName+" "+ url);
                fileTransferObject
                  .download(url, path + fileName)
                  .then(downFileResult => {
                    that.downMapList.remove(url);
                    resolve(downFileResult.toURL());
                  })
                  .catch(downFileErrText => {
                    that.downMapList.remove(url);
                    reject(downFileErrText);
                  });
              } else {
                reject("已经正在下载中");
              }
            })
            .catch(errText => {
              reject(errText);
            });
        });
      }
    }
    return result;
  }

  /**
   * 取消下载操作
   *
   * @param {string} url
   * @memberof lightFileTransfer
   */
  abortDownload(url: string): void {
    if (!!url) {
      if (this.downMapList.containkey(url)) {
        var fileTransferObject = this.downMapList.getValue(
          url
        ) as FileTransferObject;
        if (!!fileTransferObject) {
          fileTransferObject.abort();
        }
        this.downMapList.remove(url);
      }
    }
  }

  /**
   * 得到文件的路径
   *
   * @private
   * @param {LightFileType} fileType
   * @returns {Promise<any>}
   * @memberof LightFileTransfer
   */
  private _getFilePath(
    fileType: LightFileType = LightFileType.RootFile
  ): Promise<any> {
    var result: Promise<any> = null;
    switch (fileType) {
      case LightFileType.RootFile:
        result = this.lightFile.getRootFilePath();
        break;
      case LightFileType.DataFile:
        result = this.lightFile.getFilePath();
        break;
      case LightFileType.DataCache:
        result = this.lightFile.getCachePath();
        break;
      case LightFileType.DataPicture:
        result = this.lightFile.getPicturePath();
        break;
      default:
        result = this.lightFile.getRootFilePath();
        break;
    }
    return result;
  }

  /**
   * 得到文件的扩展名称
   * @private
   * @returns {string}
   * @memberof lightFileTransfer
   */
  private _getFileExtentName(url: string): string {
    var result: string = "";
    var target = "/";
    if (LightStringUtil.contain(url, target)) {
      var index = url.indexOf(target);
      result = url.substr(
        index + target.length,
        url.length - index - target.length
      );
    }
    return result;
  }
}



