import { Injectable } from "@angular/core";
import { Platform } from "ionic-angular";
import { ZBar, ZBarOptions } from "@ionic-native/zbar";
import { LightNation } from "../common/lightNation";

/**
 * 本地npm的安装:npm install --save @ionic-native/zbar
 * 加载ionic插件:ionic cordova plugin add cordova-plugin-cszbar
 * 平台:Android | iOS
 * 二维码
 * @param {ZBar} zbar
 * @memberof LightScan
 */
@Injectable()
export class LightScan extends LightNation {
  //zbar参数信息
  private options: ZBarOptions = null;

  constructor(private platform: Platform, private zbar: ZBar) {
    super();
    this.options = {
      text_title: "扫码",
      text_instructions: "请把摄像头对准二维码进行扫描",
      camera: "back", //values: "front", "back" Default: "back"
      flash: "off", //values: "on", "off", "auto" Default: "auto"
      drawSight: false
    };
  }

  //扫描二维码信息
  public scan(resultDefault: any = null): Promise<any> {
    var that = this;
    if (!!!resultDefault) {
      resultDefault =
        "http://localhost:8225/Projects/SCYK/MyForms/ASSETCOMMON/PrintCard2.aspx?AssetDocUNID=05BEE97B2A354677B0B5C97D926D2359";
    }
    var result: Promise<any> = this._getDefaultResult(resultDefault);
    if (this.platform.is("cordova")) {
      if (!!that.zbar) {
        result = new Promise(function(resolve, reject) {
          that.zbar
            .scan(that.options)
            .then(result => {
              resolve(result);
            })
            .catch(errText => {
              reject(errText);
            });
        });
      } else {
        console.log("zbar插件未安装");
      }
    }
    return result;
  }
}



